<template>
    <div class="form-box" >
        <FormItem class="form-item zindex4" :label="$t('wait.signalSource')">
            <NewSelect :module="module" v-model:realVal="waitData.ActionPara.TargetSdo.LinkSdo" :options="signalSources"
                @valChng="waitData.ActionPara.TargetSdo.LinkSdo = $event"></NewSelect>
        </FormItem>
        <FormItem class="form-item zindex3" :label="$t('wait.waitingSignalValue')">
            <NewSelect :module="module" v-model:realVal="waitData.ActionPara.CompareValue" :options="signalVals"
                @valChng="waitData.ActionPara.CompareValue = $event"></NewSelect>
        </FormItem>
        <FormItem class="form-item" :label="$t('wait.timeOutDuration')">
            <InputVal :module="module" :minNum="1" v-model:sensorValue="waitData.ActionPara.Target.Constant"   uname="time"  ></InputVal>
        </FormItem>
    </div>
   
</template>
<script>
import { deepCopy } from '@/utils/deepCopy'
import { waitForSignalData } from './wait'
export default {
    components: {
    },
    data() {
        return {
            module: "PROFILE",
            waitData: waitForSignalData(),
            signalSources: { [this.$t('wait.synSignal')]: "0x300409" },
            signalVals: { [this.$t('wait.lowLevel')]: false },
            activeTab: 1,
        }
    },
    methods: {
        initData() {
            this.waitData = deepCopy(this.stepData)
        },
        sendDataStatus() {
            this.$emit('update:stepData', this.waitData);
           console.log('waitData update:stepData', this.waitData);
        },
    },
    props: {
        stepData: {
            type: Object,
            default: waitForSignalData()
        },
    },
    created() {
        this.initData()
        this.$watch(() =>this.waitData, () => {
            this.sendDataStatus();
        },{deep:true})
    },
    watch: {

    },
    computed: {

    }
}
</script>
<style scoped>
.form-item {
    position: relative;
    width: 100%;
    height: 3.9063vh;
    box-sizing: border-box;
    margin-bottom: 1.2rem;
    left: 5vw;
    z-index: 1;
}
.form-box {
    margin-top: 1.25rem;
    position: relative;
}
.zindex3 {
    z-index: 3;
}
.zindex4 {
    z-index: 4;
}
</style>