<template>
    <div class="tech-box">
        <div class="tech-basemsg-line">
            <span class="tech-num">{{ $t("tchnlgy.prcsid") }}:</span>
            <div v-if="curPsetId != -1" class="tech-select">
                {{ curPsetId.toString().padStart(3, "0") }}
            </div>
            <span class="tech-name">{{ $t("tchnlgy.prcsname") }}</span>
            <InputText :module="module" v-if="curPsetId != -1" class="tech-input" width="24vw" :disabled="true"
                :text="profile_name" @update:modelValue="profile_name = $event.text" :textValid="nameReason == ''"
                :validReason="nameReason">
            </InputText>
            <svg-loader v-if="curPsetId != -1" @click="showGlobalConfig" class="tech-setting" icon="StaticSetting"
                size="2.5rem"></svg-loader>
        </div>
        <!--  -->
        <div class="mask-box">
            <div class="items-box">
                <div class="visual-box" v-if="activeSeqIndex === 0 || seqItems.length === 0">
                    <div class="add-btn-box" @click="showAddingSeqItemDialog(0)">
                        <svg-loader class="add-icon-class" icon="Add" size="2.84rem"></svg-loader>
                        <span class="add-text-class">
                            {{ $t("tchnlgy.add") }}
                        </span>
                    </div>
                </div>
                <div class="visual-box" v-for="(seq_item, i) in seqItems" :key="i">
                    <div :class="i === this.activeSeqIndex ? 'big-box' : 'small-box'" @click="selSeqItemByIndex(i)">
                        <span :class="i === this.activeSeqIndex ? 'big-num' : 'small-num'">{{ i + 1 }}</span>
                        <svg-loader class="delete-class" @click="delSeqItem(i)" v-show="activeSeqIndex == i"
                            icon="ActiveRedTrash"></svg-loader>
                        <div :class="i === this.activeSeqIndex
                            ? 'big-first-line'
                            : 'small-first-line'
                            ">
                            <svg-loader :icon="i == activeSeqIndex
                                ? seqIcons[seq_item.Descr][1]
                                : seqIcons[seq_item.Descr][0]
                                " :class="i === this.activeSeqIndex ? 'big-step-icon' : 'small-step-icon'"
                                :size="activeSeqIndex == i ? '3rem' : '1.5rem'"></svg-loader>
                            <span :class="i === this.activeSeqIndex
                                ? 'big-step-text'
                                : 'small-step-text'
                                ">{{ seq_types[seq_item.Descr] }}</span>
                        </div>
                        <div v-for="(items, index) in seqItemTexts(seq_item.Descr, i)" :key="index" :class="i === this.activeSeqIndex
                            ? 'big-second-line'
                            : 'small-second-line'
                            ">
                            <span :class="i === this.activeSeqIndex
                                ? 'big-param-text'
                                : 'small-param-text'
                                ">{{ items[0] }}</span>
                            <span :class="i === this.activeSeqIndex
                                ? 'big-param-val'
                                : 'small-param-val'
                                ">{{ items[1] }}</span>
                        </div>
                    </div>
                    <div class="add-btn-box" @click.stop="showAddingSeqItemDialog(i + 1)"
                        v-if="activeSeqIndex === i || activeSeqIndex == i + 1">
                        <svg-loader class="add-icon-class" icon="Add" size="2.84rem"></svg-loader>
                        <span class="add-text-class">
                            {{ $t("tchnlgy.add") }}
                        </span>
                    </div>
                </div>
            </div>
        </div>
        <div class="tech-options-box" ref="addingbox" :tabindex="99" v-show="addSeqIndex != -1 && curPsetId != -1"
            :contenteditable="true" @blur="closeSeqAddingBox">
            <div v-for="(item, type_name) in seq_types" :key="item" class="one-pro" :contenteditable="false"
                @click="addSeqItemBySeqType(type_name)">
                <input type="radio" v-show="false" name="one" class="select-one" />
                <span class="pro-name">{{ item }}</span>
            </div>
        </div>
        <!--  -->
        <div class="pro-step-data" v-if="activeSeqIndex != -1 && curPsetId != -1">
            <FindBolt v-if="sel_seq_type === 'FindBolt'" :stepList="jumpStepLst()" @confirm="confirm"
                v-model:stepData="seqItems[activeSeqIndex]">
            </FindBolt>
            <FastMove v-if="sel_seq_type === 'FastMove'" :stepList="jumpStepLst()" @confirm="confirm"
                v-model:stepData="seqItems[activeSeqIndex]">
            </FastMove>
            <Wait v-if="sel_seq_type === 'Wait'" :stepList="jumpStepLst()" @confirm="confirm"
                v-model:stepData="seqItems[activeSeqIndex]">
            </Wait>
            <SocketRelease v-if="sel_seq_type === 'SocketRelease'" :stepList="jumpStepLst()" @confirm="confirm"
                v-model:stepData="seqItems[activeSeqIndex]">
            </SocketRelease>
            <Tightening v-if="sel_seq_type === 'Tighten'" :stepList="jumpStepLst()" @confirm="confirm"
                v-model:stepData="seqItems[activeSeqIndex]">
            </Tightening>
            <OutSignal v-if="sel_seq_type === 'OutSignal'" :stepList="jumpStepLst()" @confirm="confirm"
                v-model:stepData="seqItems[activeSeqIndex]">
            </OutSignal>
            <SlopeTest v-if="sel_seq_type === 'SlopeTest'" :stepList="jumpStepLst()" @confirm="confirm"
                v-model:stepData="seqItems[activeSeqIndex]">
            </SlopeTest>
        </div>
    </div>
</template>

<script>
import { deepCopy } from "@/utils/deepCopy";
// 
import { globalConfigDialog } from "@/dialogs/profile/globalConfig";
// 
import FindBolt from "@/components/profiles/FindBolt.vue";
import FastMove from "@/components/profiles/FastMove.vue";
import Tightening from "@/components/profiles/Tightening.vue";
import SlopeTest from "@/components/profiles/SlopeTest.vue";
import SocketRelease from "@/components/profiles/SocketRelease.vue";
import Wait from "@/components/profiles/Wait.vue";
import OutSignal from "@/components/profiles/OutSignal.vue";
import {
    genSeqItem,
    genProfile,
    fillSeqItemsWithDatalog,
} from "@/components/profiles/profileHelper";
import InputText from "@/components/input/InputText.vue";
// 
import cnfrmWin from "@/dialogs/common/confirm/cnfrm";
import { messageApi } from "@/components/message/message";
// 
import { pfModApi, curPfPsetApi, curPfListApi } from "@/api/profile/profile";
// import getEmptyProfile from '../utils/empty_profile'
export default {
    components: {
        InputText,
        FindBolt,
        FastMove,
        Tightening,
        SocketRelease,
        Wait,
        OutSignal,
        SlopeTest,
    },
    data() {
        return {
            module: "PROFILE",
            /**
             * 
             */
            seqItems: [],
            /**
             * 
             */
            activeSeqIndex: -1,
            /**
             * 
             */
            addSeqIndex: -1,
            /**
             * 
             */
            sel_seq_type: "",
            /**
             * 
             */
            profile: {},
            /**
             *  Json
             */
            profile_json: "",
            /**
             * Json
             */
            global_paras_json: "",
            /**
             * -
             */
            seq_types: {
                FindBolt: this.$t("knowhat.konwhat"),
                FastMove: this.$t("fastrundown.fastrundown"),
                // "selftapprgrm": this.$t('selftapping.selftapprgrm'),
                Wait: this.$t("delay.delay"),
                // "sync" : this.$t('sync.sync'),
                SocketRelease: this.$t("unloading.unloading"),
                Tighten: this.$t("tighten.tighten"),
                OutSignal: this.$t("output.output"),
                SlopeTest: this.$t("seat.seatDetection"),
                //"ng" : ""
            },
            /**
             * - 
             */
            seqIcons: {
                FindBolt: ["StaticKnowHat", "ActiveKnowHat"],
                FastMove: ["StaticFastRotate", "ActiveFastRotate"],
                // "":  ['StaticAutoFire', 'ActiveAutoFire'],
                Wait: ["StaticWait", "ActiveWait"],
                // "":  ['StaticSync', 'ActiveSync'],
                SocketRelease: ["StaticRelease", "ActiveRelease"],
                Tighten: ["StaticLock", "ActiveLock"],
                // "": ['ActiveNright', 'ActiveNright']
                OutSignal: ["StaticOutSignal", "ActiveOutSignal"],
                SlopeTest: ["SlopeStatic", "SlopeActive"],
            },
            // 
            profile_name: "...",
            // pset()
            validPset: true,
            // pset,
            curPsetId: -1,
            /**
             * 
             */
            profileNameLst: [],
            /**
             * 
             */
            need_check_edit: false,
            delPsetNum: 0,
            isCheckRoute: false,
        };
    },
    beforeRouteLeave(to, from, next) {
        if (this.nameReason != "" && this.curPsetId != -1) {
            messageApi.warning([this.$t("job.invalidName")]);
            next(false);
            return;
        }
        this.isCheckRoute = true
        let check_del_same_pset = this.profile.Pset == this.delPsetNum
        let global_unchanged = this.global_paras_json == JSON.stringify(this.profile.Sequence.GlobalParams);
        let seq_items_unchanged = this.profile_json == JSON.stringify(this.seqItems);
        let name_unchanged = this.profile_name == this.profile.Name;
        if (global_unchanged && seq_items_unchanged && name_unchanged || check_del_same_pset) {
            next();
        } else {
            let title = this.$t("tchnlgy.savePset");
            let confirmText = this.$t("tchnlgy.yes");
            let cancelText = this.$t("tchnlgy.no");
            let msg = this.$t("tchnlgy.whetherSavePset");
            if (global_unchanged == false) msg += this.$t("tchnlgy.stepModified");
            if (seq_items_unchanged == false) msg += this.$t("tchnlgy.stepParamModified");
            if (name_unchanged == false) msg += this.$t("tchnlgy.nameModified");
            cnfrmWin({ title, confirmText, cancelText, msg })
                .then(async () => {
                    await this.confirm()
                    next();
                })
                .catch(() => {
                    next();
                });
        }
    },
    methods: {
        async confirm() {
            if (this.nameReason != "" && this.curPsetId != -1) {
                messageApi.warning([this.$t("job.invalidName")]);
                return;
            }
            this.profile.Sequence.Branchs[0].SeqItems = fillSeqItemsWithDatalog(
                deepCopy(this.seqItems)
            );
            this.profile.Name = this.profile_name;
            await this.sendFileToServerApi(this.profile, this.saveSuccess);
            this.need_check_edit = false;
            if (this.curPsetId == this.$store.state.g_status.pset) //
            {
                this.$store.state.g_status.eopos = -1;
            }
        },
        /**
         *  
         * */
        showAddingSeqItemDialog(i) {
            this.addSeqIndex = i;
            setTimeout(() => {
                this.$refs.addingbox.focus();
            }, 20);
        },
        /**
         * 
         * */
        showGlobalConfig() {
            let GlobalParams = this.profile.Sequence.GlobalParams;
            globalConfigDialog({ GlobalParams })
                .then((res) => {
                    this.profile.Sequence.GlobalParams = res;
                    this.need_check_edit = true;
                })
                .catch((err) => {
                    err && console.log(err);
                });
            this.global_paras_json = JSON.stringify(this.profile.Sequence.GlobalParams);
        },
        // 
        jumpStepLst() {
            const stepList = {
                [this.$t("fastrundown.continued")]: -1,
                [this.$t("fastrundown.stop")]: -2,
                [this.$t("fastrundown.reverseEnd")]: -3
            };
            if (this.activeSeqIndex == -1 || this.seqItems.length == 0) {
                return stepList;
            }
            for (let i = 1; i < this.seqItems.length + 1; i++) {
                if (i == this.activeSeqIndex + 1) continue;
                stepList[i] = i;
            }
            return stepList;
        },
        // 
        resetData() {
            this.getPfList();
            this.closeSeqAddingBox();
            this.sel_seq_type = "";
            this.curPsetId = -1;
            this.profile_name = "...";
            this.seqItems = [];
            this.need_check_edit = false;
            // if (this.profile)
            //     this.profile.Sequence.GlobalParams = "{}"
            //this.global_paras_json = '{}'
        },
        //div
        closeSeqAddingBox() {
            this.addSeqIndex = -1;
        },
        //
        backStaticBox() {
            this.activeBox = -1;
        },
        // ,success,failure;
        async sendFileToServerApi(Profile, success) {
            if (!this.enabledEdit) {
                return messageApi.warning([this.$t("tchnlgy.isRunning"), this.$t("tchnlgy.noModified")]);
            }

            try {
                const result = await pfModApi({ Profile })
                this.need_check_edit = false;
                success && success(result);
            } catch (err) {
                console.log("pfModApi profile error ", err);
                messageApi.error([this.$t("tchnlgy.saveFail")]);

            }
        },
        // pset,success,failure,
        getFileContentByPsetApi(pset, success, failure) {
            curPfPsetApi(pset)
                .then((result) => {
                    success && success(result);
                })
                .catch((err) => {
                    console.log("getFileContent profile error ", err);
                    failure && failure(err);
                });
        },
        // 
        getCurPsetProfile(pset) {
            if (pset && pset > 0) {
                this.getFileContentByPsetApi(
                    pset,
                    (result) => {
                        if (result.status === 0) {
                            let file = result.data.Profile;
                            this.setFileContent(file);
                            this.curPsetId = pset;
                            this.need_check_edit = true;
                        } else {
                            this.curPsetId = -1;
                            this.need_check_edit = false;
                            messageApi.warning([""]);
                        }
                    },
                    () => {
                        this.curPsetId = -1;
                        this.need_check_edit = false;
                        messageApi.warning([""]);
                    }
                );
            }
        },
        // 
        setFileContent(file) {
            this.profile_name = file.Name;
            this.profile = file;
            this.seqItems = deepCopy(
                this.profile.Sequence.Branchs[0].SeqItems.slice(1, -1)
            );
            this.profile_json = JSON.stringify(this.seqItems);
            this.global_paras_json = JSON.stringify(
                this.profile.Sequence.GlobalParams
            );
            //
            this.selSeqItemByIndex(0, false);
        },
        //0
        selSeqItemByIndex(i, anti_shake = true) {
            if (i < 0 || i >= this.seqItems.length) {
                this.activeSeqIndex = -1;
                this.sel_seq_type = "";
                return;
            }
            if (this.activeSeqIndex == i && anti_shake)
                //
                return;
            this.activeSeqIndex = i;
            //
            this.sel_seq_type = "";
            setTimeout(() => {
                this.sel_seq_type = this.seqItems[i].Descr;
            }, 30);
            // console.log("--", i, "--", this.seqItems[i], this.sel_seq_type);
        },
        // 
        addSeqItemBySeqType(seq_type) {
            if (!this.enabledEdit) {
                let notEdit = this.$t("tchnlgy.notEdit");
                let activateFile = this.$t("tchnlgy.activateFile");
                messageApi.warning([notEdit, activateFile]);
                return;
            }
            let newStepIndex =
                this.activeSeqIndex == this.addSeqIndex
                    ? this.activeSeqIndex
                    : this.activeSeqIndex + 1;
            //
            let tmp = deepCopy(this.seqItems);
            let _seqitem = genSeqItem(seq_type);
            tmp.splice(newStepIndex, 0, _seqitem);
            for (let index = 0; index < tmp.length; index++) {
                if (tmp[index].NOKAction.JumpRow > newStepIndex) {
                    tmp[index].NOKAction.JumpRow += 1;
                }
                if (tmp[index].NOKAction.NoOkJump > newStepIndex) {
                    tmp[index].NOKAction.NoOkJump += 1;
                }
            }
            this.seqItems = tmp;
            this.addSeqIndex = -1;
            this.selSeqItemByIndex(newStepIndex, false);
        },
        /**
         * 
         */
        delSeqItem(i) {
            if (!this.enabledEdit) {
                let notEdit = this.$t("tchnlgy.notEdit");
                let runFile = this.$t("tchnlgy.runFile");
                messageApi.warning([notEdit, runFile]);
                return;
            }
            for (let j = 0; j < this.seqItems.length; j++) {
                if (j == i) {
                    continue;
                }
                if (this.seqItems[j].NOKAction.JumpRow == i + 1) {
                    let result = j + 1
                    messageApi.warning([
                        this.$t('tchnlgy.isUsed', [result]), this.$t('tchnlgy.isFrom')
                    ]);
                    return;
                }
                if (this.seqItems[j].NOKAction.NoOkJump == i + 1) {
                    let result = j + 1
                    messageApi.warning([
                        this.$t('tchnlgy.isUsed', [result]), this.$t('tchnlgy.isFromNok')
                    ]);
                    return;
                }
            }
            let title = this.$t('tchnlgy.delPset')
            let confirmText = this.$t('tchnlgy.yes')
            let cancelText = this.$t('tchnlgy.no')
            let msg = this.$t("tchnlgy.isDeletePocessStep");
            cnfrmWin({ title, confirmText, cancelText, msg }).then(() => {
                let tmp = deepCopy(this.seqItems);
                //
                for (let index = 0; index < tmp.length; index++) {
                    if (tmp[index].NOKAction.JumpRow > i) {
                        tmp[index].NOKAction.JumpRow -= 1;
                    }
                    if (tmp[index].NOKAction.NoOkJump > i) {
                        tmp[index].NOKAction.NoOkJump -= 1;
                    }
                }
                //
                tmp.splice(i, 1);
                this.seqItems = tmp;
                //
                this.selSeqItemByIndex(
                    i >= this.seqItems.length - 1 ? this.seqItems.length - 1 : i,
                    false
                );
            });
        },
        promptUserSave(i) {
            let title = this.$t("tchnlgy.confirmSave");
            let confirmText = this.$t("tchnlgy.yes");
            let cancelText = this.$t("tchnlgy.no");
            let msg = this.$t("tchnlgy.confirmMsgOne");
            cnfrmWin({ title, confirmText, cancelText, msg })
                .then(() => {
                    this.selSeqItemByIndex(i);
                })
                .catch(() => {
                    this.selSeqItemByIndex(i);
                });
        },
        /** 
         * @param {*} type_name 
         * @param {*} i  
         */
        seqItemTexts(type_name, i) {
            switch (type_name) {
                // 
                case "FindBolt":
                    return [[
                        this.$t("step.angle"),
                        this.displayVal("angle", this.seqItems[i].ActionPara.Target),
                    ],
                    [
                        this.$t("step.torque"),
                        this.displayVal("torque", this.seqItems[i].ActionPara.Limit.Max),
                    ],
                    ];
                // 
                case "FastMove":
                    if (this.seqItems[i].Action == "ForceOpenLoop") {
                        return [
                            [
                                this.$t("step.torque"),
                                this.displayVal(
                                    "torque",
                                    this.seqItems[i].ActionPara.Target.Constant
                                ),
                            ],
                            [
                                this.$t("step.speed"),
                                this.displayVal(
                                    "angle_speed",
                                    this.seqItems[i].ActionPara.Velocity.Constant
                                ),
                            ],
                        ];
                    }
                    return [
                        [
                            this.$t("step.angle"),
                            this.displayVal(
                                "angle",
                                this.seqItems[i].ActionPara.Target.Constant
                            ),
                        ],
                        [
                            this.$t("step.speed"),
                            this.displayVal(
                                "angle_speed",
                                this.seqItems[i].ActionPara.Velocity.Constant
                            ),
                        ],
                    ];
                // 
                case "Wait":
                    return [
                        [
                            this.$t("wait.type") + ':',
                            this.seqItems[i].Action == "WaitConstantTime" ? "Time" : "Signal",
                        ],
                        [
                            this.$t("wait.time") + ':',
                            this.displayVal(
                                "time",
                                this.seqItems[i].ActionPara.Target.Constant
                            ),
                        ],
                    ];

                // 
                case "OutSignal":
                    return [
                        [
                            this.$t("output.type"),
                            "Signal",
                        ],
                        [
                            this.$t("output.signal"),
                            this.seqItems[i].ActionPara.CompareValue ? this.$t("output.highLevel") : this.$t("output.lowLevel"),
                        ],
                    ];

                // 
                case "SlopeTest":
                    return [
                        [
                            this.$t("seat.slope") + ':',
                            this.seqItems[i].ActionPara.Target.Constant,
                        ],
                        [
                            this.$t("seat.speed") + ':',
                            this.displayVal(
                                "angle_speed",
                                this.seqItems[i].ActionPara.Velocity
                            ),
                        ],
                    ];
                // 
                case "SocketRelease":
                    return [
                        [
                            this.$t("step.angle"),
                            this.displayVal("angle", this.seqItems[i].ActionPara.Target),
                        ],
                        [
                            this.$t("step.torque"),
                            this.displayVal("torque", this.seqItems[i].ActionPara.Limit.Max),
                        ],
                    ];
                // 
                case "Tighten":
                    if (this.seqItems[i].Action == "ForceOpenLoop") {
                        return [
                            [
                                this.$t("step.torque"),
                                this.displayVal(
                                    "torque",
                                    this.seqItems[i].ActionPara.Target.Constant
                                ),
                            ],
                            [
                                this.$t("step.speed"),
                                this.displayVal(
                                    "angle_speed",
                                    this.seqItems[i].ActionPara.Velocity
                                ),
                            ],
                        ];
                    } else if (this.seqItems[i].Action == "PositionOpenLoop") {
                        return [
                            [
                                this.$t("step.angle"),
                                this.displayVal(
                                    "angle",
                                    this.seqItems[i].ActionPara.Target.Constant
                                ),
                            ],
                            [
                                this.$t("step.speed"),
                                this.displayVal(
                                    "angle_speed",
                                    this.seqItems[i].ActionPara.Velocity
                                ),
                            ],
                        ];
                    }
                    return [
                        [
                            this.$t("tchnlgy.target"),
                            this.displayVal(
                                "torque",
                                this.seqItems[i].ActionPara.Target.Constant
                            ),
                        ],
                        [
                            this.$t("step.speed"),
                            this.displayVal(
                                "angle_speed",
                                this.seqItems[i].ActionPara.Velocity
                            ),
                        ],
                    ];
                default:
                    return [
                        [
                            this.$t("tchnlgy.target"),
                            this.displayVal(
                                "torque",
                                this.seqItems[i].ActionPara.Target.Constant
                            ),
                        ],
                        [
                            this.$t("step.speed"),
                            this.displayVal(
                                "angle_speed",
                                this.seqItems[i].ActionPara.Velocity
                            ),
                        ],
                    ];
            }
        },
        /**
         * 
         * @param {*} sname  
         * @param {*} sensor_val  
         */
        displayVal(sname, sensor_val) {
            var sensor = this.$store.state.sensors[sname];
            if (sensor == null) {
                sensor = { gain: 1, offset: 0, name: "" };
            }
            return `${Math.round(
                (parseFloat(sensor_val) * sensor.gain + sensor.offset) * 1000
            ) / 1000
                }${sensor.name}`;
        },
        // 
        initEmptyProfile() {
            this.profile = genProfile(2);
            this.setFileContent(this.profile);
        },
        // psetquery
        initCurPset() {
            let pset = this.$route.query.pset;
            if (typeof parseInt(pset) == "number" && pset > 0) {
                this.curPsetId = pset;
            } else {
                this.curPsetId = -1;
            }
        },
        // 
        excuteDefaultAction() {
            this.resetData();
            this.curPsetId = this.$route.query.pset;
            if (this.curPsetId > 0) {
                this.getCurPsetProfile(this.curPsetId);
            }
        },
        // pset
        // updateFstatusEditingPset(pset) {
        //     this.$store.state.f_status.editingPset = pset
        // },
        // 
        getPfList(success = undefined) {
            curPfListApi()
                .then((result) => {
                    if (result.status == 9127) {
                        return;
                    }
                    if (result.status != 0) {
                        return;
                    }
                    this.profileNameLst = [];
                    result.data.forEach((file) => {
                        if (file.pset != this.curPsetId)
                            this.profileNameLst.push(file.name);
                    });
                    success && success();
                })
                .catch(() => { });
        },
        saveSuccess(result) {
            if (result.status == 0) {
                this.profile_json = JSON.stringify(this.seqItems);
                this.global_paras_json = JSON.stringify(
                    this.profile.Sequence.GlobalParams
                );
                // let msg = this.$t('settable.success')
                messageApi.success([this.$t('setop.saveSuc')])
            } else if (result.status == 9131) {
                messageApi.warning([this.$t('tchnlgy.fileUnchanged')]);
            } else if (result.status == 9125) {
                messageApi.warning([this.$t('tchnlgy.fileNotExit')]);
            } else if (result.status == 9128) {
                messageApi.warning([this.$t('tchnlgy.versionInconsistent')]);
            } else if (result.status == 9129) {
                messageApi.warning([this.$t('tchnlgy.modifyFileFail')]);
            } else {
                messageApi.error([this.$t('tchnlgy.saveFail'), [`[${result.status}]`]]);
            }
        },
        //
        checkDelPfPset() {
            this.$bus.on('delPset', (data) => {
                this.delPsetNum = data.pset
                if (this.profile == '') return
            })
        },
        isChanged() {
            let check_del_same_pset = this.profile.Pset == this.delPsetNum
            let global_unchanged = this.global_paras_json == JSON.stringify(this.profile.Sequence.GlobalParams);
            let seq_items_unchanged = this.profile_json == JSON.stringify(this.seqItems);
            let name_unchanged = this.profile_name == this.profile.Name;
            if (global_unchanged && seq_items_unchanged && name_unchanged || check_del_same_pset) {
                return false
            } else {
                return true
            }
        },
        openDialog() {
            let title = this.$t("tchnlgy.savePset");
            let confirmText = this.$t("tchnlgy.yes");
            let cancelText = this.$t("tchnlgy.no");
            let msg = this.$t("tchnlgy.isSavePset");
            cnfrmWin({
                title,
                confirmText,
                cancelText,
                msg
            }).then(() => {
                this.confirm()
                this.excuteDefaultAction();
            }).catch(() => {
                this.excuteDefaultAction();
            });
        }
    },
    computed: {
        enabledEdit() {
            return (
                (this.curPsetId == this.$store.state.g_status.pset &&
                    this.$store.state.g_status.busy) == false
            );
        },
        nameReason() {
            // ,
            if (this.curPsetId == -1) return -1;
            // 1,2,-1
            if (this.profile_name.length == 0) {
                return this.$t('dlgprogram.nameNotEmpty')
            }
            if (
                this.profile_name && this.profileNameLst.length > 0 &&
                this.profileNameLst.indexOf(this.profile_name) != -1
            ) {
                return this.$t('dlgprogram.nameRepeat')
            }
            return "";
        },
        // enabled() {
        //     if (
        //         "PROFILE" in this.$store.state.f_status.permissions &&
        //         this.$store.state.f_status.permissions["PROFILE"] == 4
        //     )
        //         return true;
        //     else return false;
        // },
        // pointerEvent() {
        //     return this.enabled ? "auto" : "none";
        // },
        // opacity() {
        //     return this.enabled ? 1 : 0.5;
        // },
    },
    created() {
        this.getPfList();
        this.initEmptyProfile();
        this.checkDelPfPset()
    },
    watch: {
        "$route.query.pset": {
            immediate: true,
            handler: function (from, to) {
                if (to != undefined && from != undefined) {
                    if (this.isChanged() && this.isCheckRoute == false) {
                        this.openDialog()
                    } else {
                        this.excuteDefaultAction();
                    }
                } else {
                    this.excuteDefaultAction();
                }
            },
        },
    },
};
</script>

