                v-model:sensorValue="tightenData.ActionPara.ResultTorque.Min"></InputVal>
        </FormItem>




        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item torque-rate"
            :label="$t('tighten.torqueRateThreshold')">
            <InputVal :module="module" :minNum="tightenData.ActionPara.ResultTorque.Min"
                :maxNum="tightenData.ActionPara.ResultTorque.Max" height="2.4rem" fontSize="1.25rem" width="6.4rem"
                uname="torque" :showUnitName="false" v-model:sensorValue="tightenData.ActionPara.TorqueRateThreshold">
            </InputVal>
        </FormItem>
       
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item angle-threshold"
            :label="$t('tighten.angleThreshold')">
            <InputVal :module="module" :minNum="tightenData.ActionPara.ResultTorque.Min"
