            <InputVal :module="module" height="3rem" fontSize="1.25rem" width="6.4rem" uname="angle" :showUnitName="false"
                :maxNum="tightenData.ActionPara.ResultAngle.Max"
                v-model:sensorValue="tightenData.ActionPara.ResultAngle.Min">
            </InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item target-plastic-angle"
            :label="$t('tighten.targetPlasticAngle')">
            <InputVal :module="module" height="3rem" fontSize="1.25rem" width="6.4rem" uname="angle" :showUnitName="false"
                :maxNum="tightenData.ActionPara.ResultAngle.Max"
