            </InputVal>
        </FormItem>


        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item torque-mode-max-angle"
            :label="$t('tighten.maxAngle')">
            <InputVal :module="module" height="3rem" fontSize="1.25rem" width="6.4rem" uname="angle" :showUnitName="false"
                :minNum="tightenData.ActionPara.ResultAngle.Min" :maxNum="tightenData.ActionPara.Limit.Max"
                :sensorValue="tightenData.ActionPara.ResultAngle.Max">
            </InputVal>
        </FormItem>
        <FormItem labelSize="0" leftDis="0" labelWidth="0rem" class="form-item abort-angle" label="">
