<template>
    <div class="form-box" >
        <FormItem class="form-item" :label="$t('wait.time')">
            <InputVal :module="module" :minNum="1" uname="time"
                v-model:sensorValue="waitData.ActionPara.Target.Constant"></InputVal>
        </FormItem>
    </div>
</template>
<script>
import { deepCopy } from '@/utils/deepCopy'
import { waitForTimeData } from './wait'
export default {
    components: {

    },
    data() {
        return {
            module: "PROFILE",
            waitData: waitForTimeData(),
            activeTab: 1,
        }
    },
    methods: {
        initData() {
            this.waitData = deepCopy(this.stepData)
        },
        sendDataStatus() {
            this.$emit('update:stepData', this.waitData)
        },
 
    },
    props: {
        stepData: {
            type: Object,
            default:  waitForTimeData()
        },
    },
    created() {
        this.initData()
        this.$watch(() =>this.waitData, () => {
            this.sendDataStatus();
        },{deep:true})
    },
    watch: {

    },
    computed: {

    }
}
</script>
<style scoped>
.form-item {
    position: relative;
    width: 100%;
    height: 3.9063vh;
    box-sizing: border-box;
    margin-bottom: 1.2rem;
    left: 5vw;
    z-index: 1;
}
.form-box {
    margin-top: 1.25rem;
    position: relative;
}

</style>