<template>
    <div class="data-box">
        <div class="lock-tab">
            <div @click="activeTab = 1">
                <span :class="{ 'tab-text': true, 'border-green': activeTab == 1 }">{{ $t('hightsetting.policymonitoring')
                }}</span>
            </div>
            <div @click="activeTab = 2"><span :class="{ 'tab-text': true, 'border-green': activeTab == 2 }">{{
                $t('hightsetting.advancedSettings') }}</span>
            </div>
        </div>
        <div class="form-box" v-show="activeTab == 1">
            <!-- <FormItem class="form-item zindex6" :label="$t('strategy.mode')">
                <NewSelect :module="module" :options="tightenModes" :realVal="tightenMode" @valChng="tightenModeChg">
                </NewSelect>
            </FormItem> -->
            <SlopTestMode v-if="activeTab == 1" v-model:stepData="tightenData" />

        </div>
        <div class="form-box" v-show="activeTab == 2">
            <TightenAdvanceConfig v-if="activeTab == 2" v-model:stepData="tightenData"
                :stepList="stepList" />
        </div>
        <LeetxButton :module="module" width="30.3125rem" height="3.75rem" @click="confirm" textColor="#fff"
            :text="$t('strategy.save')" fontFamily="'SourceHanSansCN-Bold'" fontSize="2.25rem" bgcColor="#00c155"
            class="confirm-box"></LeetxButton>
    </div>
</template>
<script>
import { deepCopy } from '@/utils/deepCopy'
import { getTightenDatabyMode} from './tightening'
import { slopeTestData } from './slopetest'
import SlopTestMode from './SlopTestMode.vue'
import TightenAdvanceConfig from './TightenAdvanceConfig.vue'
// import getVirtualData from '@/utils/virtualdata'
// import { messgaeApi } from '../message/message'
export default {
    components: {
        SlopTestMode,
        TightenAdvanceConfig
    },
    data() {
        return {
            module: "PROFILE",
            activeTab: 1,
            tightenData: slopeTestData(),
            /**
             * @filed   
             * 0->  
             * 1->  
             * 2->  
             * 3->-  
             * 4->  
             * 5->  
             * 6->  
             */
            tightenMode: 2,
            /**
             * @'':0,'':1,'':2,'-':3,'':4,'':5,'':6
             */
            tightenModes: { [this.$t('strategy.torqueMode')]: 2, [this.$t('strategy.angleMode')]: 4, [this.$t('strategy.yieldMode')]: 7 },
        }
    },
    props: {
        stepData: {
            type: Object,
            default: slopeTestData(),
        },
        stepList: {
            type: Object,
            default: function () {
                return { "": -1, "": -2 }
            }
        }
    },
    methods: {
        updateData(keys, data) {
            let type = keys[0]
            let param = keys[1]
            let params = ["angleControl", "torqueControl", "angleControl", "ngMotion", "backDir", "jumpTarget", "checkTouchPoint", "dir", "angleClear", "torqueClear", "angleValueMode", "torqueValueMode", "JumpRow"]
            if (params.indexOf(param) !== -1) {
                this.tightenData[type][param].sensorValue = data
                if (param === 'dir') {
                    this.angleDir = data
                }
            } else {
                this.tightenData[type][param] = data
            }
        },
        initOptions() {
            this.tightenData = deepCopy(this.stepData)
        },
        confirm() {
            // 
            this.$emit('confirm')
        },
        dispatchDataChanged() {

        },
        initData() {
            // switch(this.stepData.Action){
            //     case 'ForceOpenLoop':
            //         this.tightenMode =2;
            //         break;
            //     case 'PositionOpenLoop':
            //         this.tightenMode = 4;
            //         break;
            //     case 'YeildPoint':
            //         this.tightenMode = 7;
            //         break;
            //     default:
            //         this.tightenMode = 7;

            // }
            this.tightenMode = this.stepData.Action == "ForceOpenLoop" ? 2 : 4;
            this.tightenData = this.stepData;
            this.$watch(() => this.tightenData, () => {
                this.$emit('update:stepData', this.tightenData);
                // console.log('tighten dataChnged -> ', this.tightenData);
            }, { deep: true })
        },
        tightenModeChg(newval) {
            console.log('newval', newval);
            this.tightenMode = newval;
            this.tightenData = getTightenDatabyMode(newval)
        },
    },
    watch: {
        // stepData: {
        //     deep: true,
        //     immediate: true,
        //     handler: function () {
        //         // this.initData()
        //     }
        // },
    },
    created() {
        this.initOptions()
        this.initData()
    },
    computed: {
        strategyMinTorqueReason() {
            if (this.strategyMinTorqueType == 1) {
                return this.$t('strategy.msgFour')
            }
            return ""
        },
        strategyMinAngleReason() {
            if (this.strategyMinAngleType == 1) {
                return this.$t('strategy.msgTwo')
            }
            return ""
        },
        controlMinTorqueReason() {
            if (this.controlMinTorqueType == 1) {
                return this.$t('tchnlgy_monitor.msgTwo')
            }
            return ""
        },
        controlMinAngleReason() {
            if (this.controlMinAngleType == 1) {
                return this.$t('tchnlgy_monitor.msgThree')
            }
            return ""
        },

    }
}
</script>
<style scoped>
.data-box {
    position: relative;
    width: 100%;
    height: 64.2969vh;
    overflow: hidden;
}

.form-box {
    margin-top: 1.25rem;
    position: relative;
}

.lock-tab {
    position: relative;
    width: 88vw;
    left: 50%;
    transform: translateX(-50%);
    height: 4.375vh;
    display: flex;
}

.lock-tab>div {
    position: relative;
    font-size: var(--font1_75);
    flex: 1;
}

.zindex6 {
    z-index: 6;
}

.tab-text {
    display: block;
    width: auto;
    position: absolute;
    left: 50%;
    font-size: var(--font1_75);
    top: 50%;
    transform: translate(-50%, -50%);
    border-bottom: 4px solid transparent;
    white-space: nowrap !important;

}

.border-green {
    border-bottom: 4px solid var(--C00c155);
}

.form-item {
    position: relative;
    width: 100%;
    height: 3.9063vh;
    box-sizing: border-box;
    margin-bottom: 1.2rem;
    left: 5vw;
}

.confirm-box {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 3.4688vh;
}
</style>