<template>
    <div class="form-box" >
        <FormItem class="form-item  first-item zindex4" :label="$t('wait.signalSource')">
            <NewSelect :module="module" v-model:realVal="waitData.ActionPara.TargetSdo.LinkSdo" :options="signalSources"
                @valChng="waitData.ActionPara.TargetSdo.LinkSdo = $event"></NewSelect>
        </FormItem>
        <FormItem class="form-item zindex3" :label="$t('output.signalType')">
            <NewSelect :module="module" v-model:realVal="waitData.ActionPara.CompareValue" :options="signalVals"
                @valChng="waitData.ActionPara.CompareValue = $event"></NewSelect>
        </FormItem>

        <LeetxButton :module="module" width="30.3125rem" height="3.75rem" @click="confirm" textColor="#fff"
            :text="$t('knowhat.save')" fontFamily="'SourceHanSansCN-Bold'" fontSize="2.25rem" bgcColor="#00c155"
            class="confirm-box"></LeetxButton>


    </div>
   
</template>
<script>
import { deepCopy } from '@/utils/deepCopy'
import { waitForSignalData } from './wait'
import LeetxButton from '../controls/LeetxButton.vue'
export default {
    components: {
        LeetxButton
    },
    data() {
        return {
            module: "PROFILE",
            waitData: waitForSignalData(),
            signalSources: { [this.$t('output.ioSignal')]: "0x30082800" },
            signalVals: { [this.$t('output.lowLevel')]: false, [this.$t('output.highLevel')]: true},
            activeTab: 1,
        }
    },
    methods: {
        confirm() {
            this.$emit('confirm')
        },
        initData() {
            this.waitData = deepCopy(this.stepData)
        },
        sendDataStatus() {
            this.$emit('update:stepData', this.waitData);
           console.log('waitData update:stepData', this.waitData);
        },
    },
    props: {
        stepData: {
            type: Object,
            default: waitForSignalData()
        },
    },
    created() {
        this.initData()
        this.$watch(() =>this.waitData, () => {
            this.sendDataStatus();
        },{deep:true})
    },
    watch: {

    },
    computed: {

    }
}
</script>
<style scoped>

.first-item {
    margin-top: 3.7484vh;
}

.form-item {
    position: relative;
    width: 100%;
    height: 3.9063vh;
    box-sizing: border-box;
    margin-bottom: 1.2rem;
    left: 5vw;
    z-index: 1;
}
.form-box {
    margin-top: 1.25rem;
    position: relative;
    height: 64.2969vh;
    overflow: hidden;
}
.zindex3 {
    z-index: 3;
}
.zindex4 {
    z-index: 4;
}

.confirm-box {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 5.4688vh;
}
</style>