<template>
    <div class="form-box">
        <FormItem class="form-item zindex6" :label="$t('tighten.torqueValueMode')">
            <NewSelect :module="module" :options="{ [$t('tighten.peakValue')]: 0 }"
                v-model:realVal="tightenData.TorqueValueMode"></NewSelect>
        </FormItem>
        <FormItem class="form-item zindex5" :label="$t('tighten.angleValueMode')">
            <NewSelect :module="module" :options="{ [$t('tighten.peakValue')]: 0 }"
                v-model:realVal="tightenData.AngleValueMode"></NewSelect>
        </FormItem>
        <FormItem class="form-item" leftDis="3.8rem" :label="$t('tighten.angleReduction')">
            <LeetxSwitch :module="module" :activeVal="true" :staticVal="false" :height="2" :value="tightenData.AngleZero"
                v-model="tightenData.AngleZero"></LeetxSwitch>
        </FormItem>
        <FormItem class="form-item" leftDis="3.8rem" :label="$t('tighten.torqueReset')">
            <LeetxSwitch :module="module" :activeVal="true" :staticVal="false" :height="2" :value="tightenData.TorqueZero"
                v-model="tightenData.TorqueZero"></LeetxSwitch>
        </FormItem>
        <FormItem class="form-item" :label="$t('release.timeout')">
            <InputVal :module="module" :minNum="2000" uname="time" v-model:sensorValue="tightenData.ActionPara.Timeout_mS">
            </InputVal>
        </FormItem>
        <FormItem class="form-item" :label="$t('release.stepDelay')">
            <InputVal :module="module" uname="time" v-model:sensorValue="tightenData.Delay_ms"></InputVal>
        </FormItem>
        <FormItem class="form-item" :label="$t('release.errorJump')" :zIndex="31">
            <NewSelect :module="module" :isScroll="true" :options="stepList"
                v-model:realVal="tightenData.NOKAction.JumpRow"></NewSelect>
        </FormItem>
        <FormItem class="form-item" :label="$t('release.NoOkJump')">
            <NewSelect :module="module" :isScroll="true" :options="stepList"
                v-model:realVal="tightenData.NOKAction.NoOkJump"></NewSelect>
        </FormItem>
       
    </div>
</template>
<script>
import { tightenAngleData } from './tightening'
import { deepCopy } from '@/utils/deepCopy'
import LeetxSwitch from '@/components/switch/LeetxSwitch.vue'
import InputVal from '@/components/input/InputVal.vue'

export default {
    components: {
        LeetxSwitch,
        InputVal
    },
    data() {
        return {
            module: "PROFILE",
            tightenData: tightenAngleData(),
        }
    },
    props: {
        stepData: {
            type: Object,
            default: tightenAngleData(),
        },
        stepList: {
            type: Object,
            default: function () {
                return { "": -1, "": -2 }
            }
        },
        visible: {
            type: Boolean,
            default: true,
        },
    },
    methods: {
        initOptions() {
            this.tightenData = deepCopy(this.stepData);
        },
        sendDataStatus() {
            this.$emit('update:stepData', this.tightenData)
        },
    },
    created() {
        // this.$watch(() => this.visible, () => {
        //   if(this.visible)
        this.initOptions();
        // },{immediate:true});
        this.$watch(() => this.tightenData, () => {
            this.sendDataStatus();
        }, { deep: true });
    },
    computed: {

    }
}
</script>
<style scoped>
.form-box {
    margin-top: 1.25rem;
    position: relative;
}

.zindex5 {
    z-index: 5;
}

.zindex6 {
    z-index: 6;
}

.form-item {
    position: relative;
    width: 100%;
    height: 3.9063vh;
    box-sizing: border-box;
    margin-bottom: 1.2rem;
    left: 5vw;
}
.forbit-color{
    color: var(--Cb0)!important;
}
</style>