<template>
    <div class="set-tech-box" v-if="isShow">
        <div class="head-text-line">
            <span>{{ $t('hightsetting.advancedSettings') }}</span>
            <span class="cancel-box" @click="cancel">
                <svgloader class="tech-setting" icon="Close" size="1.2rem"></svgloader>
            </span>
        </div>
        <div class="time-line">
            <span class="tech-num">{{ $t('hightsetting.startingTorque') }}</span>
            <InputVal :module="module" v-model:sensorValue="globalParams.ThresholdValue" class="tech-input" width="18rem"
                uname="torque">
            </InputVal>
        </div>
        <div class="dots-line">
            <span class="pro-text">{{ $t('hightsetting.globalAngleMonitoring') }}</span>
            <CheckCircle :module="module" v-model="globalParams.GlobalAngleMonitoring" :activeValue="true"
                :staticValue="false" width="2.5rem" class="is-use-global-angle"></CheckCircle>
            <div class="tech-input">
                <InputVal :module="module" :textValid='angleRangeValid' v-model:sensorValue="globalParams.AngleLimit.Min"
                    :isAllowUse="globalParams.GlobalAngleMonitoring" class="min-torque" uname="angle"
                    :show-unit-name="false" width="8rem"></InputVal>
                <div class="step-line">~</div>
                <InputVal :module="module" :textValid='angleRangeValid' v-model:sensorValue="globalParams.AngleLimit.Max"
                    :isAllowUse="globalParams.GlobalAngleMonitoring" class="max-torque" uname="angle" width="8rem">
                </InputVal>
            </div>
        </div>
        <div class="sampling-line">
            <span class="sampling-rate">{{ $t('hightsetting.samplingRate') }}</span>
            <NewSelect :module="module" :options="frequencys" v-model:realVal="globalParams.SampleFrequency"
                class="sampling-select">
            </NewSelect>
        </div>
        <div class="max-line">
            <span class="max-samples">{{ $t('hightsetting.maxSamples') }}</span>
            <NewSelect :module="module" :options="samplingDots" v-model:realVal="globalParams.SamplingNumber"
                class="max-samples-select">
            </NewSelect>
        </div>
        <!-- <div class="calibration-line">
            <span class="enable-calibration">{{ $t('hightsetting.enableCalibration') }}</span>
            <LeetxSwitch :value="globalParams.CalibrationeOn" @sentVal="globalParams.CalibrationeOn = $event"
                class="clbrt-btn" :activeVal="true" :staticVal="false" :height="2"></LeetxSwitch>
        </div> -->
        <div class="dir-line">
            <span class="default-dir">{{ $t('hightsetting.defRotationDirection') }}</span>
            <NewSelect :module="module" :options="dirs" v-model:realVal="globalParams.Direction" class="default-dir-btn">
            </NewSelect>
        </div>

        <div class="reverse-speed-line">
            <span class="reverse-speed-num">{{ $t('hightsetting.reverseSpeedValue') }}</span>
            <InputVal :module="module" v-model:sensorValue="globalParams.ReverseSpeedValue" class="reverse-speed-input"
                width="18rem" uname="angle_speed">
            </InputVal>
        </div>

        <div class="reverse-angle-line">
            <span class="reverse-angle-num">{{ $t('hightsetting.reverseAngleValue') }}</span>
            <InputVal :module="module" v-model:sensorValue="globalParams.ReverseAngleValue" class="reverse-angle-input"
                width="18rem" uname="angle">
            </InputVal>
        </div>

        <div class="ergonomic-line">
            <span class="ergonomic-text">{{ $t('hightsetting.ergonomicStop') }}</span>
            <LeetxSwitch :module="module" class="ergonomic-switch" :activeVal="true" :staticVal="false"
                :value="globalParams.EnableSoftStop" @sentVal="globalParams.EnableSoftStop = $event" :height="2.5">
            </LeetxSwitch>
        </div>

        <div class="discharge-time-line">
            <span class="discharge-time-text" :class="{ 'forbit-color': !globalParams.EnableSoftStop }">{{
                $t('hightsetting.dischargeTime') }}</span>
            <InputVal :module="module" class="discharge-time-input" :isAllowUse='globalParams.EnableSoftStop'
                v-model:sensorValue="globalParams.SoftStopTime" width="18rem" uname="discharge_time">
            </InputVal>
        </div>
        <LeetxButton bgcColor="#00c155" textColor="#fff" width="30.3125rem" fontSize="2.25rem" class="confirm-box"
            :text="$t('confirm.validity')" @click="confirmInner"></LeetxButton>
    </div>
</template>
<script>

import svgloader from '@/components/icon_font/svgloader.vue'
import InputVal from '@/components/input/InputVal.vue'
import NewSelect from '@/components/select/NewSelect.vue'
import LeetxButton from '@/components/controls/LeetxButton.vue'
import CheckCircle from '@/components/controls/CheckCircle.vue'
import { maskApi } from '../mask/StackMask.vue'
import { deepCopy } from '@/utils/deepCopy'
import LeetxSwitch from '@/components/switch/LeetxSwitch.vue'
// import cnfrmWin from '../common/confirm/cnfrm'
import { messageApi } from '@/components/message/message'
export default {
    components: {
        InputVal,
        svgloader,
        LeetxButton,
        CheckCircle,
        NewSelect,
        LeetxSwitch
    },
    data() {
        return {
            module: "PROFILE",
            frequencys: { '100sps': 100, '250sps': 250, '500sps': 500, '1000sps': 1000 },
            dirs: { [this.$t('hightsetting.foreward')]: "CW", [this.$t('hightsetting.reverse')]: "CCW" },
            samplingDots: { '1000': 1000, '2000': 2000, '5000': 5000, '10000': 10000 },
            isShow: true,
            maskId: 0,
            globalParams: undefined,
            angleRangeValid: true,
        }

    },
    methods: {
        cancel() {
            this.isShow = false
            maskApi.descend(this.maskId)
        },
        confirmInner() {
            if (!this.angleRangeValid) {
                let msgOne = this.$t('hightsetting.globalMonitpers')
                let msgTwo = this.$t('hightsetting.wrongRange')
                return messageApi.warning([msgOne, msgTwo])
            }
            this.isShow = false
            // let msg = this.$t('settable.success')
            // messageApi.success([msg])
            this.onConfirm && this.onConfirm(this.globalParams)
            maskApi.descend(this.maskId)
        },
        modData(key, val) {
            const paramKeys = ['ThresholdValue', 'AngleMin', 'AngleMax']
            if (paramKeys.indexOf(key) !== -1) {
                if (key === 'ThresholdValue') {
                    this.globalParams[key] = val.sensorValue
                } else if (key === 'AngleMin') {
                    this.globalParams.AngleLimit.Min = val.sensorValue
                } else {
                    this.globalParams.AngleLimit.Max = val.sensorValue
                }
            } else {
                this.globalParams[key] = val
            }
        },
        initStatus() {
            this.maskId = maskApi.rise(15)
        },
        initGlobalParams() {
            this.globalParams = deepCopy(this.GlobalParams)
        }
    },
    created() {
        this.initGlobalParams()
    },
    mounted() {
        this.initStatus()
    },
    props: {
        GlobalParams: {
            "AutoPowerOn": true,
            "AutoPowerOff": false,
            // 
            "Direction": "CW",
            // 
            "SampleFrequency": 1000,//hz
            // 
            "SamplingNumber": 10000,
            // 
            "CalibrationeOn": true,//
            // 
            "GlobalAngleMonitoring": true,
            "ThresholdValue": 0,//
            "AngleLimit": {
                "Min": -50,
                "Max": 50
            },
            "ReverseSpeedValue": 10,
            "ReverseAngleValue": 0,
            "EnableSoftStop": true,
            "SoftStopTime": 100,
        },
    },
    computed: {

    },
    watch: {
        globalParams: {
            deep: true,
            handler: function () {
                this.angleRangeValid = (this.globalParams.AngleLimit.Min < this.globalParams.AngleLimit.Max)
            }
        }

    }

}
</script>
