<template>
    <div class="data-box">
        <div class="lock-tab">
            <div @click="activeTab = 1">
                <span :class="{ 'tab-text': true, 'border-green': activeTab == 1 }">{{ $t('hightsetting.policymonitoring')
                }}</span>
            </div>
            <div @click="activeTab = 2"><span :class="{ 'tab-text': true, 'border-green': activeTab == 2 }">{{
                $t('hightsetting.advancedSettings') }}</span>
            </div>
        </div>
        <div class="form-box" v-show="activeTab == 1">
            <FormItem class="form-item first-item zindex5" :label="$t('wait.wait')">
                <NewSelect :module="module" :realVal="wait_type" :options="waitTypes" @valChng="changeWaitype"></NewSelect>
            </FormItem>
            <WaitForTime v-if="wait_type == 0 && activeTab == 1" v-model:stepData="waitData"></WaitForTime>
            <WaitForSignal v-if="wait_type == 1 && activeTab == 1" v-model:stepData="waitData"></WaitForSignal>
        </div>
        <div class="form-box" v-show="activeTab == 2">
            <WaitAdvanceConfig v-if="activeTab == 2" v-model:stepData="waitData" :stepList="stepList" />
        </div>
        <LeetxButton :module="module" width="30.3125rem" height="3.75rem" @click="confirm" textColor="#fff"
            :text="$t('tchnlgy.save')" fontFamily="'SourceHanSansCN-Bold'" fontSize="2.25rem" bgcColor="#00c155"
            class="save-box"></LeetxButton>
    </div>
</template>
<script>
import { deepCopy } from '@/utils/deepCopy'
import LeetxButton from '../controls/LeetxButton.vue'
import WaitForSignal from './WaitForSignal.vue'
import WaitForTime from './WaitForTime.vue'
import WaitAdvanceConfig from './WaitAdvanceConfig.vue'
import { waitForSignalData, waitForTimeData } from './wait.js'
export default {
    components: {
        LeetxButton,
        WaitForSignal,
        WaitForTime,
        WaitAdvanceConfig
    },
    data() {
        return {
            module: "PROFILE",
            waitData: waitForSignalData(),
            // 0 ,1 
            wait_type: 0,
            waitTypes: { [this.$t('wait.time')]: 0, [this.$t('wait.signal')]: 1 },
            activeTab: 1,
        }
    },
    methods: {
        changeWaitype(val) {
            this.wait_type = val;
            this.waitData = this.wait_type == 1 ? waitForSignalData() : waitForTimeData();
        },
        initData() {
            this.waitData = deepCopy(this.stepData)
            this.wait_type = this.waitData.Action == "WaitForSignal" ? 1 : 0;
        },
        confirm() {
            this.$emit('confirm')
        },
        getUnit(name) {
            return this.$store.state.sensors[name]
        },
        sendDataStatus() {
            this.$emit('update:stepData', this.waitData)
        },
        updateWaitSignal(key, e) {
            this.waitData[key].sensorValue = e
        }
        // ,
    },
    props: {
        stepData: {
            type: Object,
            default: waitForSignalData()
        },
        stepList: {
            type: Object,
            default: function () {
                return { "": -1, "": -2 }
            }
        }
    },
    created() {
        this.initData()
        this.$watch(() => this.waitData, () => {
            this.sendDataStatus();
        }, { deep: true });
    },
    watch: {
        // stepData: {
        //     deep: true,
        //     immediate:true,
        //     handler: function () {
        //         this.initData()
        //     } 
        // },
        // waitData: {
        // deep: true,
        // handler: function () {
        //     this.sendDataStatus()
        // }
        // },
        // mode() {
        //     this.waitData.mode.sensorValue = this.mode
        //     this.waitData.mode.displayValue = this.mode
        // },
    },
    computed: {
        // enabled() {
        //     if (this.module in this.$store.state.f_status.permissions && this.$store.state.f_status.permissions[this.module] == 4)
        //         return true
        //     else
        //         return false
        // },
        // pointerEvent() {
        //     return this.enabled ? "auto" : "none";
        // },
        // opacity() {
        //     return this.enabled ? 1 : 0.5;
        // }
    }
}
</script>
<style scoped>
.data-box {
    position: relative;
    width: 100%;
    height: 64.2969vh;
    overflow: hidden;
}

.lock-tab {
    position: relative;
    width: 88vw;
    left: 50%;
    transform: translateX(-50%);
    height: 4.375vh;
    display: flex;
}

.lock-tab>div {
    position: relative;
    font-size: var(--font1_75);
    flex: 1;
}

.tab-text {
    display: block;
    width: auto;
    position: absolute;
    left: 50%;
    font-size: var(--font1_75);
    top: 50%;
    transform: translate(-50%, -50%);
    border-bottom: 4px solid transparent;
    white-space: nowrap !important;

}

.border-green {
    border-bottom: 4px solid var(--C00c155);
}

.form-item {
    position: relative;
    width: 100%;
    height: 3.9063vh;
    box-sizing: border-box;
    margin-bottom: 1.2rem;
    left: 5vw;
    z-index: 1;
}

.first-item {
    margin-top: 3.7484vh;
}

.form-box {
    margin-top: 1.25rem;
    position: relative;
}

.zindex5 {
    z-index: 5;
}

.save-box {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 5.4688vh;
    background-color: #00c155;
}</style>