<template>
    <div class="form-box">
        <div class="torque-unit">
            {{ $t('monitor.torque') }}/{{ sensorUnit('torque') }}
        </div>
        <FormItem class="form-item zindex5" :label="$t('monitor.speed')">
            <InputVal :module="module" width='17.625rem' uname="angle_speed" @updateData="updateData"
                v-model:sensorValue="tightenData.ActionPara.Velocity"></InputVal>
        </FormItem>
        <FormItem class="form-item" :label="$t('fastrundown.accleratetime')">
            <InputVal :module="module" v-model:sensorValue="tightenData.ActionPara.AccTime" @updateData="updateData"
                uname="acc_time">
            </InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item torque-mode-max-torque"
            :label="$t('selftapping.maxtorque')">
            <InputVal :module="module" height="2.4rem" fontSize="1.25rem" width="6.4rem" uname="torque"
                :showUnitName="false" :minNum="tightenData.ActionPara.Target.Constant"
                :maxNum="this.$store.state.g_status.sensor_max * 1.2"
                v-model:sensorValue="tightenData.ActionPara.ResultTorque.Max"></InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item target-torque"
            :label="$t('strategy.targetTorque')">
            <InputVal :module="module" :minNum="tightenData.ActionPara.ResultTorque.Min"
                :maxNum="tightenData.ActionPara.ResultTorque.Max" height="2.4rem" fontSize="1.25rem" width="6.4rem"
                uname="torque" :showUnitName="false" v-model:sensorValue="tightenData.ActionPara.Target.Constant">
            </InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item min-torque"
            :label="$t('selftapping.mintorque')">
            <InputVal :module="module" height="2.4rem" fontSize="1.25rem" width="6.4rem" uname="torque"
                :showUnitName="false" :maxNum="tightenData.ActionPara.Target.Constant"
                v-model:sensorValue="tightenData.ActionPara.ResultTorque.Min"></InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item limit-torque"
            :label="$t('release.torqueThreshold')">
            <InputVal :module="module" height="3rem" fontSize="1.25rem" width="6.4rem" uname="torque" :showUnitName="false"
                v-model:sensorValue="tightenData.ActionPara.ThresholdValue"></InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item min-angle"
            :label="$t('tighten.miniAngle')">
            <InputVal :module="module" height="3rem" fontSize="1.25rem" width="6.4rem" uname="angle" :showUnitName="false"
                :maxNum="tightenData.ActionPara.ResultAngle.Max"
                v-model:sensorValue="tightenData.ActionPara.ResultAngle.Min">
            </InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item torque-mode-max-angle"
            :label="$t('tighten.maxAngle')">
            <InputVal :module="module" height="3rem" fontSize="1.25rem" width="6.4rem" uname="angle" :showUnitName="false"
                :minNum="tightenData.ActionPara.ResultAngle.Min" :maxNum="tightenData.ActionPara.Limit.Max"
                v-model:sensorValue="tightenData.ActionPara.ResultAngle.Max">
            </InputVal>
        </FormItem>
        <FormItem labelSize="0" leftDis="0" labelWidth="0rem" class="form-item safe-angle" label="">
            <InputVal :module="module" height="3rem" fontSize="1.25rem" width="6.4rem" uname="angle" :showUnitName="false"
                :minNum="tightenData.ActionPara.ResultAngle.Max"  v-model:sensorValue="tightenData.ActionPara.Limit.Max"></InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="0rem" class="form-item safe-angle-text"
            :label="$t('fastrundown.safeAngle')">
        </FormItem>
        <div class="zero">
            <span>0.0</span>
        </div>
        <div class="angle-unit">
            {{ $t('monitor.angle') }}/{{ sensorUnit('angle') }}
        </div>
        <svg-loader class="locktorque-icon" icon='LockTorque' size="34rem"></svg-loader>
    </div>
</template>
<script>
import { deepCopy } from '@/utils/deepCopy'
import { tightenTorqueData } from './tightening'
export default {
    data() {
        return {
            module: "PROFILE",
            tightenData: tightenTorqueData(),
        }
    },
    props: {
        stepData: {
            type: Object,
            default: tightenTorqueData(),
        },
        stepList: {
            type: Object,
            default: function () {
                return { "": -1, "": -2 }
            }
        }
    },
    methods: {
        updateData() {
            if (this.tightenData.ActionPara.AccTime == 0)
                this.tightenData.ActionPara.AccTime = 0.000001
            this.tightenData.ActionPara.Acc.Constant = this.tightenData.ActionPara.Velocity / this.tightenData.ActionPara.AccTime;
        },
        init() {
            this.tightenData = deepCopy(this.stepData)
        },
        sendDataStatus() {
            this.$emit('update:stepData', this.tightenData)
            console.log('torque update:stepData', this.tightenData)
        },
        sensorUnit(name) {
            return this.$store.state.sensors[name].name
        },
    },

    watch: {

    },
    created() {
        this.init()
        this.$watch(() => this.tightenData, () => {
            this.sendDataStatus();
        }, { deep: true })
    },
    computed: {

    }
}
</script>
<style scoped>
.form-box {
    margin-top: 1.25rem;
    position: relative;
}

.zindex5 {
    z-index: 5;
}

.form-item {
    position: relative;
    width: 100%;
    height: 3.9063vh;
    box-sizing: border-box;
    margin-bottom: 1.2rem;
    left: 5vw;
}

.torque-unit {
    position: absolute;
    left: 15.3125rem;
    top: 8rem;
    z-index: 2;
    font-size: 1.25rem;
}

.angle-unit {
    position: absolute;
    left: 42.3125rem;
    top: 25.5rem;
    z-index: 2;
    font-size: 1.25rem;
}

.torque-mode-max-torque,
.angle-mode-max-torque,
.target-torque,
.min-torque,
.limit-torque {
    position: absolute;
    left: 1rem;
    top: 10rem;
    z-index: 2;
}

.target-torque {
    top: 12.8rem;
}

.min-torque {
    top: 15.8rem;
}

.limit-torque {
    top: 22.2rem;
}

.locktorque-icon {
    position: absolute;
    top: 5.2rem;
    left: 13.6312rem;
    width: 30rem;
    height: 30rem;
}

.min-angle,
.torque-mode-max-angle,
.angle-mode-max-angle,
.safe-angle,
.target-angle,
.safe-angle-text {
    position: absolute;
    top: 29rem;
    z-index: 2;
}

.min-angle {
    top: 29.5rem;
    left: 17.2rem;
}

.zero {
    position: absolute;
    top: 28.1rem;
    left: 2.3rem;
    width: 100%;
    height: 100%;
    font-size: 1.25rem;
    color: #969696;
}

.torque-mode-max-angle {
    top: 32.3rem;
    left: 24rem;
}

.safe-angle {
    left: 34rem;
}

.safe-angle-text {
    font-size: 1.25rem;
    left: 42rem;
}
</style>