<template>
    <div class="data-box">
        <FormItem class="form-item first-item" :label="$t('knowhat.repeatTime')">
            <InputVal :module="module" :isInt="true" :minNum="1" :maxNum="100"
                v-model:sensorValue="cfg_data.ActionPara.Repeat.RepeatCounts">
            </InputVal>
        </FormItem>
        <FormItem class="form-item zindex6" :label="$t('knowhat.directionRotation')">
            <NewSelect :module="module" :options="dirs" v-model:realVal="cfg_data.ActionPara.Direction"></NewSelect>
        </FormItem>
        <FormItem class="form-item" :label="$t('knowhat.SingleRotationAngle')">
            <InputVal :module="module" uname="angle" v-model:sensorValue="cfg_data.ActionPara.Target"></InputVal>
        </FormItem>
        <FormItem class="form-item" :label="$t('release.stoppingTorque')">
            <InputVal :module="module" uname="torque" v-model:sensorValue="cfg_data.ActionPara.Limit.Max"></InputVal>
        </FormItem>
        <div>
            <svg-loader class="hatRecognition" icon="hatRecognition" size="28rem"></svg-loader>
        </div>
        <FormItem class="form-itemb speed" :label="$t('knowhat.speed')" labelSize="1.25rem" leftDis="1.5rem"
            labelWidth="1.7rem">
            <InputVal :module="module" height="3rem" fontSize="1.25rem" width="6.4rem" uname="angle_speed"
                :showUnitName="false" @updateData="updateData('acc')" v-model:sensorValue="cfg_data.ActionPara.Velocity">
            </InputVal>
        </FormItem>
        <FormItem class="form-item speedTime" :label="$t('knowhat.acceleratetime')" labelSize="1.25rem" leftDis="1rem"
            labelWidth="4rem">
            <InputVal :module="module" height="3rem" fontSize="1.25rem" width="6.4rem" uname="acc_time"
                :showUnitName="false" v-model:sensorValue="cfg_data.ActionPara.AccTime" @updateData="updateData('acc')">
            </InputVal>
        </FormItem>
        <FormItemTwo class="repeat-delay">
            <InputVal :module="module" height="3rem" fontSize="1.25rem" width="6.4rem" uname="time" :showUnitName="false"
                v-model:sensorValue="cfg_data.ActionPara.Repeat.IntervalTime_mS"></InputVal>
            <span class="repeat-delay-text">{{ $t('knowhat.repetitionDelay') }}</span>

        </FormItemTwo>
        <div class="angle-unit angle-speed">
            {{ $t('knowhat.speed') }}/{{ sensorUnit('angle_speed') }}
        </div>
        <div class="angle-unit angle-time">
            {{ $t('knowhat.time') }}/{{ sensorUnit('time') }}
        </div>
        <LeetxButton :module="module" width="30.3125rem" height="3.75rem" @click="confirm" textColor="#fff"
            :text="$t('knowhat.save')" fontFamily="'SourceHanSansCN-Bold'" fontSize="2.25rem" bgcColor="#00c155"
            class="confirm-box"></LeetxButton>
    </div>
</template>
<script>
import InputVal from '../input/InputVal.vue'
import { deepCopy } from '@/utils/deepCopy'
import { findBoltData } from './findBolt'
import NewSelect from '../select/NewSelect.vue'
import LeetxButton from '../controls/LeetxButton.vue'

export default {
    components: {
        NewSelect,
        InputVal,
        LeetxButton
    },
    data() {
        return {
            module: "PROFILE",
            cfg_data: findBoltData(),
            dirs: { '': 1, '': -1, '': 0 },
        }
    },
    props: {
        stepData: {
            type: Object,
            default: findBoltData(),
        },
    },
    methods: {
        confirm() {
            this.$emit('confirm')
        },
        sensorUnit(uname) {
            return this.$store.state.sensors[uname].name
        },
        updateData() {
            if (this.cfg_data.ActionPara.AccTime == 0)
                this.cfg_data.ActionPara.AccTime = 0.000001;
            this.cfg_data.ActionPara.Acc.Constant = this.cfg_data.ActionPara.Velocity / this.cfg_data.ActionPara.AccTime;
        },
        sendDataStatus() {
            this.$emit('update:stepData', this.cfg_data)
        },
        init() {
            let frwrd = this.$t('knowhat.frwrd')
            let bcwrd = this.$t('knowhat.bcwrd')
            let frwrdBcwrd = this.$t('knowhat.frwrdBcwrd')
            this.dirs = { [frwrd]: 1, [bcwrd]: -1, [frwrdBcwrd]: 0 }
            this.cfg_data = deepCopy(this.stepData)
        }

    },

    created() {
        this.init()
        this.$watch(()=>this.cfg_data, () => {
            this.sendDataStatus()
        }, { deep: true });
    },
    computed: {

    }

}
</script>
<style scoped>
.data-box {
    position: relative;
    width: 100%;
    height: 64.2969vh;
    overflow: hidden;
}

.hatRecognition {
    position: absolute;
    left: 12.5569rem;
    top: 20.375rem;
    width: 31.3875rem;
    height: 18.0975rem;
}

.angle-speed {
    position: absolute;
    left: 14.5569rem;
    top: 19.70rem;
    width: 7rem;
    font-size: 1.25rem;
}

.angle-time {
    position: absolute;
    left: 40.5569rem;
    top: 34.7rem;
    width: 7rem;
    font-size: 1.25rem;
}

.form-item {
    position: relative;
    width: 100%;
    height: 3.9063vh;
    box-sizing: border-box;
    margin-bottom: 1.2rem;
    left: 5vw;
}


.first-item {
    margin-top: 3.7484vh;
}

.zindex6 {
    z-index: 6;
}

.speed {
    position: absolute;
    left: 4vw;
    top: 30.25rem;
    transform: translateY(-50%);
    color: var(--C3B) !important;
    font-size: var(--font1_625);
}

.speedTime {
    position: absolute;
    left: 9vw;
    top: 40.3rem;
    transform: translateY(-50%);
    color: var(--C3B) !important;
    font-size: var(--font1_625);
}

.repeat-delay {
    position: absolute;
    left: 19rem;
    top: 38.8rem;
}

.repeat-delay-text {
    margin-left: 0.5rem;
    font-size: 1.25rem;
}

.confirm-box {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 5.4688vh;
}
</style>