        </FormItem>

        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item torque-rate"
            :label="$t('seat.safeTorque')">
            <InputVal :module="module" :minNum="tightenData.ActionPara.ResultTorque.Max"
                :maxNum="this.$store.state.g_status.sensor_max" height="2.4rem" fontSize="1.25rem" width="6.4rem"
                uname="torque" :showUnitName="false" v-model:sensorValue="tightenData.ActionPara.TorqueLimit">
            </InputVal>
        </FormItem>
               
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item angular-sample"
            :label="$t('seat.slopeThreshold')">
            <InputVal :module="module" height="2.4rem" fontSize="1.25rem" width="6.4rem"
                :maxNum="tightenData.ActionPara.Target.Constant"  :minNum="0" uname="slope" :showUnitName="false"
                v-model:sensorValue="tightenData.ActionPara.TorqueRateThreshold">
            </InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item angle-threshold"
            :label="$t('seat.angleThreshold')">
            <InputVal :module="module" :minNum="0"
                :maxNum="tightenData.ActionPara.ResultAngle.Max" height="2.4rem" fontSize="1.25rem" width="6.4rem"
                uname="angle" :showUnitName="false" v-model:sensorValue="tightenData.ActionPara.AngleThresholdValue">
            </InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item start-torque"
            :label="$t('seat.startTorque')">
            <InputVal :module="module" :minNum="0"
