                uname="torque" :showUnitName="false" v-model:sensorValue="tightenData.ActionPara.ThresholdValue">
            </InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item target-plastic-angle"
            :label="$t('seat.delayAngle')">
            <InputVal :module="module" height="2.4rem" fontSize="1.25rem" width="6.4rem" uname="angle" :showUnitName="false"
                :maxNum="tightenData.ActionPara.ResultAngle.Max"  :minNum="0"
                v-model:sensorValue="tightenData.ActionPara.AngleDelay">
            </InputVal>
        </FormItem>

        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item target-angle"
