            <InputVal :module="module" height="2.4rem" fontSize="1.25rem" width="6.4rem" uname="angle" :showUnitName="false"
                :maxNum="tightenData.ActionPara.ResultAngle.Max"  :minNum="0"
                v-model:sensorValue="tightenData.ActionPara.AngleSampling">
            </InputVal>
        </FormItem>

        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="0rem" class="form-item min-angle"
            label="">
            <InputVal :module="module" height="2.4rem" fontSize="1.25rem" width="6.4rem" uname="angle" :showUnitName="false"
                :maxNum="tightenData.ActionPara.ResultAngle.Max"
                v-model:sensorValue="tightenData.ActionPara.ResultAngle.Min">
            </InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="0rem" class="form-item min-angle-text"
            :label="$t('seat.minAngle')">
        </FormItem>


        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="0rem" class="form-item torque-mode-max-angle"
            label="">
            <InputVal :module="module" height="2.4rem" fontSize="1.25rem" width="6.4rem" uname="angle" :showUnitName="false"
                :minNum="computeMinValueOfMax" :maxNum="tightenData.ActionPara.AngleLimit"
                v-model:sensorValue="tightenData.ActionPara.ResultAngle.Max">
            </InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="0rem" class="form-item max-angle-text"
            :label="$t('seat.maxAngle')">
        </FormItem>


        <FormItem labelSize="0" leftDis="0" labelWidth="0rem" class="form-item abort-angle" label="">
            <InputVal :module="module" height="2.4rem" fontSize="1.25rem" width="6.4rem" uname="angle" :showUnitName="false"
                :minNum="tightenData.ActionPara.ResultAngle.Max" v-model:sensorValue="tightenData.ActionPara.AngleLimit"></InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="0rem" class="form-item abort-angle-text"
            :label="$t('seat.safeAngle')">
        </FormItem>


        <FormItem labelSize="0" leftDis="0" labelWidth="0rem" class="form-item min-rate" label="">
            <InputVal :module="module" height="2.4rem" fontSize="1.25rem" width="6.4rem"  uname="slope" :showUnitName="false"
                :minNum="tightenData.ActionPara.TorqueRateThreshold" v-model:sensorValue="tightenData.ActionPara.Target.Constant"></InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="0rem" class="form-item min-rate-text"
            :label="$t('seat.targetSlope')">
        </FormItem>
        <!-- <FormItem labelSize="0" leftDis="0" labelWidth="0rem" class="form-item yield-ratio" label="">
            <InputVal :module="module" height="3rem" fontSize="1.25rem" width="6.4rem" uname="angle" :showUnitName="false"
                :minNum="tightenData.ActionPara.ResultAngle.Max" :sensorValue="tightenData.ActionPara.Limit.Max"></InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="0rem" class="form-item yield-ratio-text"
            :label="$t('tighten.yieldRatio')">
        </FormItem>
        <FormItem labelSize="0" leftDis="0" labelWidth="0rem" class="form-item max-rate" label="">
            <InputVal :module="module" height="3rem" fontSize="1.25rem" width="6.4rem" uname="angle" :showUnitName="false"
                :minNum="tightenData.ActionPara.ResultAngle.Max" :sensorValue="tightenData.ActionPara.Limit.Max"></InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="0rem" class="form-item max-rate-text"
            :label="$t('tighten.maxTorRate')">
        </FormItem> -->

        <!-- <div class="zero">
            <span>0.0</span>
        </div> -->
        <div class="angle-unit">
            {{ $t('monitor.angle') }}/{{ sensorUnit('angle') }}
        </div>
        <svg-loader class="locktorque-icon" icon='SlopeFind' size="34rem"></svg-loader>
    </div>
</template>
<script>
import { deepCopy } from '@/utils/deepCopy'
import { slopeTestData } from './slopetest'
export default {
    data() {
        return {
            module: "PROFILE",
            tightenData: slopeTestData(),
        }
    },
    props: {
        stepData: {
            type: Object,
            default: slopeTestData(),
        },
        // stepList: {
        //     type: Object,
        //     default: function () {
        //         return { "": -1, "": -2 }
        //     }
        // }
    },
    methods: {
        updateData() {
            if (this.tightenData.ActionPara.AccTime == 0)
                this.tightenData.ActionPara.AccTime = 0.000001
            this.tightenData.ActionPara.Acc.Constant = this.tightenData.ActionPara.Velocity / this.tightenData.ActionPara.AccTime;
        },
        init() {
            this.tightenData = deepCopy(this.stepData)
        },
        sendDataStatus() {
            this.$emit('update:stepData', this.tightenData)
            console.log('torque update:stepData', this.tightenData)
        },
        sensorUnit(name) {
            return this.$store.state.sensors[name].name
        },
    },

    watch: {

    },
    created() {
        this.init()
        this.$watch(() => this.tightenData, () => {
            this.sendDataStatus();
        }, { deep: true })
    },
    computed: {
        computeMinValueOfMaxAngle() {
            return Math.max(this.tightenData.ActionPara.AngleThresholdValue,this.tightenData.ActionPara.ResultAngle.Min,this.tightenData.ActionPara.AngleDelay,this.tightenData.ActionPara.AngleSampling)
        },
        computeMinValueOfMaxTorque() {
            return Math.max(this.tightenData.ActionPara.ResultTorque.Min,this.tightenData.ActionPara.ThresholdValue)
        },
    }
}
</script>
