<template>
    <div class="data-box">
        <div class="lock-tab">
            <div @click="activeTab = 1">
                <span :class="{ 'tab-text': true, 'border-green': activeTab == 1 }">{{
                    $t('hightsetting.policymonitoring')
                }}</span>
            </div>
            <div @click="activeTab = 2"><span :class="{ 'tab-text': true, 'border-green': activeTab == 2 }">{{
                $t('hightsetting.advancedSettings')
            }}</span>
            </div>
        </div>
        <div v-show="activeTab == 1">
            <div class="torque-unit">
                {{ $t('monitor.torque') }}/{{ sensorUnit('torque') }}
            </div>
            <div class="angle-unit">
                {{ $t('monitor.angle') }}/{{ sensorUnit('angle') }}
            </div>
            <FormItemTwo flexDir="column" class="add-time">
                <span class="label-text top-dis">{{ $t('strategy.addTime') }}</span>
                <InputVal :module="module" fontSize="1.25rem" height="3rem" width="6.4rem" uname="acc_time"
                    v-model:sensorValue="config.ActionPara.AccTime" @updateData="updateData('AccTime', $event)">
                </InputVal>
            </FormItemTwo>
            <FormItemTwo flexDir="column" class="release-angle">
                <span class="label-text top-dis">{{ $t('release.angleRelease') }}</span>
                <InputVal :module="module" fontSize="1.25rem" height="3rem" width="6.4rem" uname="angle"
                    :show-unit-name="false" :minNum="0" v-model:sensorValue="config.ActionPara.Target">
                </InputVal>
            </FormItemTwo>
            <FormItemTwo flexDir="column" class="limit-torque">
                <span class="label-text top-dis">{{ $t('release.torqueThreshold') }}</span>
                <InputVal :module="module" fontSize="1.25rem" height="3rem" width="6.4rem" uname="torque"
                    :show-unit-name="false" v-model:sensorValue="config.ActionPara.ThresholdValue"></InputVal>
            </FormItemTwo>

            <FormItemTwo flexDir="column" class="stop-torque">
                <span class="label-text top-dis">{{ $t('release.stoppingTorque') }}</span>
                <InputVal :module="module" fontSize="1.25rem" height="3rem" width="6.4rem" uname="torque"
                    :show-unit-name="false" v-model:sensorValue="config.ActionPara.Limit.Max"></InputVal>
            </FormItemTwo>

            <FormItemTwo flexDir="column" class="backward">
                <span class="label-text top-dis">{{ $t('release.reversal') }}</span>
                <InputVal :module="module" fontSize="1.25rem" height="3rem" width="6.4rem" uname="angle_speed"
                    :show-unit-name="false" v-model:sensorValue="config.ActionPara.Velocity"
                    @updateData="updateData('speed', $event)"></InputVal>
            </FormItemTwo>

            <div class="speed-unit">
                {{ $t('monitor.speed') }}/{{ sensorUnit('angle_speed') }}
            </div>
            <svg-loader class="release-icon" icon='Release' size="30rem"></svg-loader>
        </div>
        <div v-show="activeTab == 2">
            <FormItem class="form-item" :label="$t('release.timeout')">
                <InputVal :module="module" :minNum="2000" v-model:sensorValue="config.ActionPara.Timeout_mS" uname="time" />
            </FormItem>
            <FormItem class="form-item" :label="$t('release.stepDelay')">
                <InputVal :module="module" v-model:sensorValue="config.Delay_ms" uname="time" />
            </FormItem>
            <FormItem class="form-item" :label="$t('release.errorJump')">
                <NewSelect :module="module" :isScroll="true" :options="stepList" v-model:realVal="config.NOKAction.JumpRow"></NewSelect>
            </FormItem>
        </div>
        <LeetxButton :module="module" width="30.3125rem" height="3.75rem" @click="confirm" textColor="#fff"
            :text="$t('tchnlgy.save')" fontFamily="'SourceHanSansCN-Bold'" fontSize="2.25rem" bgcColor="#00c155"
            class="confirm-box"></LeetxButton>
    </div>
</template>
<script>
import LeetxButton from '../controls/LeetxButton.vue'
import { socketReleaseData } from './socketRelease'
import InputVal from '../input/InputVal.vue'
export default {
    components: {
        InputVal,
        LeetxButton
    },
    data() {
        return {
            module: "PROFILE",
            config: socketReleaseData(),
            activeTab: 1
        }
    },
    methods: {
        initData() {
            this.config = this.stepData;
        },
        confirm() {
            this.$emit('confirm');
        },
        updateData() {
            this.config.ActionPara.AccTime = this.config.ActionPara.AccTime == 0 ? 0.000001 : this.config.ActionPara.AccTime;
            this.config.ActionPara.Acc.Constant = this.config.ActionPara.Velocity / this.config.ActionPara.AccTime;
        },
        sendDataStatus() {
            this.$emit('update:stepData', this.config);
            console.log('update:stepData', this.config);
        },
        sensorUnit(name) {
            return this.$store.state.sensors[name].name
        },
    },
    props: {
        stepData: {
            type: Object,
            default: socketReleaseData()
        },
        stepList: {
            type: Object,
            default: function () {
                return { "": -1, "": -2 }
            }
        }

    },
    created() {
        this.initData()
        this.$watch(() => this.config, () => {
            this.sendDataStatus();
        }, { deep: true });
    },
    watch: {
        // stepData() {
        //     this.initData()
        // },
        // unloadingData: {
        //     deep: true,
        //     handler: function () {
        //         this.sendDataStatus()
        //     }
        // }
    },
    computed: {

    }

}
</script>
