    position: relative;
    width: 88vw;
    left: 50%;
    transform: translateX(-50%);
    height: 4.375vh;
    display: flex;
    margin-bottom: 2.1625rem;
}

.lock-tab>div {
    position: relative;
    font-size: var(--font1_75);
    flex: 1;
}

.tab-text {
    display: block;
