    position: absolute;
    top: 5.2rem;
    left: 14.5rem;

}

.release-angle {
    position: absolute;
    top: 10.2rem;
    left: 20.5rem;
}

.limit-torque {
    position: absolute;
    top: 18.5rem;
    left: 6.5rem;
}

.stop-torque {
    position: absolute;
    top: 30.5rem;
    left: 6.5rem;
}

.backward {
    position: absolute;
    left: 40.1325rem;
    top: 27.5rem;
}

.torque-unit {
    font-size: 1.25rem;
    position: absolute;
