<template>
    <div class="form-box">
        <div class="torque-unit">
            {{ $t('monitor.torque') }}/{{ sensorUnit('torque') }}
        </div>
        <FormItem class="form-item zindex5" :label="$t('monitor.speed')">
            <InputVal :module="module" width='6rem' uname="angle_speed" @updateData="updateData"
                v-model:sensorValue="tightenData.ActionPara.Velocity"></InputVal>
            <NewSelect :module="module" class="left-dis" :width="8" v-model:realVal="tightenData.ActionPara.Direction"
                :options="{ [$t('release.foreWard')]: 1, [$t('release.reversal')]: -1 }"></NewSelect>
        </FormItem>
        <FormItem class="form-item" :label="$t('fastrundown.accleratetime')">
            <InputVal :module="module" @updateData="updateData" uname="acc_time"
                v-model:sensorValue="tightenData.ActionPara.AccTime">
            </InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item safe-torque"
            :label="$t('strategy.safetytorque')">
            <InputVal :module="module" height="2.4rem" fontSize="1.25rem" width="6.4rem" uname="torque"
                :showUnitName="false" :minNum="tightenData.ActionPara.ResultTorque.Max"
                v-model:sensorValue="tightenData.ActionPara.Limit.Max"></InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item angle-mode-max-torque"
            :label="$t('selftapping.maxtorque')">
            <InputVal :module="module" height="2.4rem" fontSize="1.25rem" width="6.4rem" uname="torque"
                :showUnitName="false" :minNum="tightenData.ActionPara.ResultTorque.Min" :maxNum="tightenData.ActionPara.Limit.Max"
                v-model:sensorValue="tightenData.ActionPara.ResultTorque.Max"></InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item min-torque"
            :label="$t('selftapping.mintorque')">
            <InputVal :module="module" height="2.4rem" fontSize="1.25rem" width="6.4rem" uname="torque"
                :showUnitName="false" :maxNum="tightenData.ActionPara.ResultTorque.Max"
                v-model:sensorValue="tightenData.ActionPara.ResultTorque.Min"></InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item limit-torque"
            :label="$t('release.torqueThreshold')">
            <InputVal :module="module" height="3rem" fontSize="1.25rem" width="6.4rem" uname="torque" :showUnitName="false"
                v-model:sensorValue="tightenData.ActionPara.ThresholdValue"></InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item min-angle"
            :label="$t('tighten.miniAngle')">
            <InputVal :module="module" height="3rem" fontSize="1.25rem" width="6.4rem" uname="angle" :showUnitName="false"
                :maxNum="tightenData.ActionPara.Target.Constant"
                v-model:sensorValue="tightenData.ActionPara.ResultAngle.Min"></InputVal>
        </FormItem>
        <FormItem labelSize="0rem" leftDis="0" labelWidth="0rem" class="form-item angle-mode-max-angle">
            <InputVal :module="module" height="3rem" fontSize="1.25rem" width="6.4rem" uname="angle" :showUnitName="false"
                :minNum="tightenData.ActionPara.Target.Constant"
                v-model:sensorValue="tightenData.ActionPara.ResultAngle.Max"></InputVal>
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item angle-mode-max-angle-text"
            :label="$t('tighten.maxAngle')">
        </FormItem>
        <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item target-angle"
            :label="$t('fastrundown.monitorangle')">
            <InputVal :module="module" height="3rem" fontSize="1.25rem" width="6.4rem" uname="angle" :showUnitName="false"
                :minNum="tightenData.ActionPara.ResultAngle.Min" :maxNum="tightenData.ActionPara.ResultAngle.Max"
                v-model:sensorValue="tightenData.ActionPara.Target.Constant"></InputVal>
        </FormItem>
        <div class="zero">
            <span>0.0</span>
        </div>
        <div class="angle-unit">
            {{ $t('monitor.angle') }}/{{ sensorUnit('angle') }}
        </div>
        <svg-loader class="locktorque-icon" icon='LockAngle' size="34rem"></svg-loader>
    </div>
</template>
<script>
import { tightenAngleData } from './tightening'
import { deepCopy } from '@/utils/deepCopy'

export default {
    data() {
        return {
            module: "PROFILE",
            tightenData: tightenAngleData(),
        }
    },
    props: {
        stepData: {
            type: Object,
            default: tightenAngleData(),
        },
    },
    methods: {
        initOptions() {
            this.tightenData =  deepCopy (this.stepData );
        },
        updateData() {
            if (this.tightenData.ActionPara.AccTime == 0)
                this.tightenData.ActionPara.AccTime = 0.000001
            this.tightenData.ActionPara.Acc.Constant = this.tightenData.ActionPara.Velocity / this.tightenData.ActionPara.AccTime;
        },
        sendDataStatus() {
            this.$emit('update:stepData', this.tightenData)
            console.log('update:stepData', this.tightenData)
        },
        sensorUnit(name) {
            return this.$store.state.sensors[name].name
        },
    },
    created() {
        this.initOptions()
        this.$watch(() => this.tightenData, () => {
            this.sendDataStatus();
        }, { deep: true });
    },
    computed: {

    }
}
</script>
<style scoped>

.form-box {
    margin-top: 1.25rem;
    position: relative;
}

.zindex5 {
    z-index: 5;
}

.form-item {
    position: relative;
    width: 100%;
    height: 3.9063vh;
    box-sizing: border-box;
    margin-bottom: 1.2rem;
    left: 5vw;
}

.torque-unit {
    position: absolute;
    left: 15.3125rem;
    top: 8rem;
    z-index: 2;
    font-size: 1.25rem;
}

.angle-unit {
    position: absolute;
    left: 41.8rem;
    top: 25.5rem;
    z-index: 2;
    font-size: 1.25rem;
}

.torque-mode-max-torque,
.angle-mode-max-torque,
.target-torque,
.min-torque,
.limit-torque,
.safe-torque {
    position: absolute;
    left: 1rem;
    top: 9rem;
    z-index: 2;
}

.angle-mode-max-torque {
    top: 12rem;
}


.min-torque {
    top: 15rem;
}

.limit-torque {
    top: 22.5rem
}

.locktorque-icon {
    position: absolute;
    top: 5.2rem;
    left: 13.6312rem;
    width: 30rem;
    height: 30rem;
}

.left-dis {
    margin-left: 3.625rem;
}

.min-angle,
.torque-mode-max-angle,
.angle-mode-max-angle,
.safe-angle,
.target-angle,
.safe-angle-text,
.angle-mode-max-angle-text {
    position: absolute;
    top: 32rem;
    z-index: 2;
}

.min-angle {
    top: 29.3rem;
    left: 17.2rem;
}

.zero {
    position: absolute;
    top: 27.9rem;
    left: 2.3rem;
    width: 100%;
    height: 100%;
    font-size: 1.25rem;
    color: #969696;
}
.target-angle {
    top: 32.5rem;
    left: 22rem;
}
.angle-mode-max-angle {
    top: 29rem;
    left: 32.5rem;
}
.angle-mode-max-angle-text {
    font-size: 1.25rem;
    left: 41rem;
    top: 29rem;
}
</style>