import { createApp } from "vue";
import langvue from "@/plugins/langVue";
import SetTech from "./GlobalConfig.vue"

export const globalConfigDialog = (props) => {
    return new Promise((resolve, reject) => { //promiseokresolvenoreject
        // 
        let dom = createApp(SetTech,props).use(langvue).mount(document.createElement("div"));
        // 
        document.body.appendChild(dom.$el);
        // 
        dom.onConfirm = (x) => {
            // 
            resolve(x)
        }
        dom.onCancel = function (err) {
            // 
            reject(err)
        }
    })
}
export default {globalConfigDialog}