<template>
    <div class="data-box">
        <div class="lock-tab">
            <div @click="activeTab = 1">
                <span :class="{ 'tab-text': true, 'border-green': activeTab == 1 }">{{
                    $t("hightsetting.policymonitoring")
                }}</span>
            </div>
            <div @click="activeTab = 2">
                <span :class="{ 'tab-text': true, 'border-green': activeTab == 2 }">{{
                    $t("hightsetting.advancedSettings")
                }}</span>
            </div>
        </div>
        <div v-show="activeTab == 1">
            <div class="torque-unit">{{ $t("monitor.torque") }}/{{ sensorUnit("torque") }}</div>
            <div class="angle-unit">{{ $t("monitor.angle") }}/{{ sensorUnit("angle") }}</div>
            <FormItem class="form-item select-cnrl" :label="$t('strategy.mode')">
                <NewSelect :module="module" :options="fitMethods" :realVal="dataMethod" @valChng="modeChanged">
                </NewSelect>
            </FormItem>
            <FormItem class="form-item" :label="$t('monitor.speed')">
                <InputVal :module="module" v-model:sensorValue="fastmoveData.ActionPara.Velocity.Constant"
                    uname="angle_speed" @updateData="cal_acc"></InputVal>
            </FormItem>
            <FormItem class="form-item" :label="$t('fastrundown.accleratetime')">
                <InputVal :module="module" v-model:sensorValue="fastmoveData.ActionPara.AccTime" uname="acc_time"
                    @updateData="cal_acc"></InputVal>
            </FormItem>
            <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" v-if="dataMethod == 1"
                class="form-item target-torque" :label="$t('fastrundown.targettorque')">
                <InputVal :module="module" :minNum="this.$store.state.g_status.sensor_max * 0.01" height="3rem"
                    fontSize="1.25rem" width="6.4rem" uname="torque" :showUnitName="false"
                    v-model:sensorValue="fastmoveData.ActionPara.Target.Constant">
                </InputVal>
            </FormItem>
            <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" v-if="dataMethod == 0" class="form-item safe-torque"
                :label="$t('fastrundown.safeTorque')">
                <InputVal :module="module" height="3rem" fontSize="1.25rem" width="6.4rem" uname="torque"
                    :showUnitName="false" v-model:sensorValue="fastmoveData.ActionPara.Limit.Max"></InputVal>
            </FormItem>
            <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" v-if="dataMethod == 0"
                class="form-item limit-torque" :label="$t('fastrundown.torqueThreshold')">
                <InputVal :module="module" height="3rem" fontSize="1.25rem" width="6.4rem" uname="torque"
                    :showUnitName="false" v-model:sensorValue="fastmoveData.ActionPara.ThresholdValue"></InputVal>
            </FormItem>
            <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" v-if="dataMethod == 0"
                class="form-item target-angle" :label="$t('fastrundown.monitorangle')">
                <InputVal :module="module" height="3rem" fontSize="1.25rem" width="6.4rem" uname="angle"
                    :showUnitName="false" v-model:sensorValue="fastmoveData.ActionPara.Target.Constant">
                </InputVal>
            </FormItem>
            <svg-loader class="fastmove-icon" :icon="dataMethod == 0 ? 'FastAngle' : 'FastTorque'"
                size="30rem"></svg-loader>
            <div class="zero">
                <span>0.0</span>
            </div>
        </div>
        <div v-show="activeTab == 2">

            <FormItem class="form-item" :label="$t('release.timeout')">
                <InputVal :module="module" :minNum="2000" v-model:sensorValue="fastmoveData.ActionPara.Timeout_mS"
                    uname="time">
                </InputVal>
            </FormItem>
            <FormItem class="form-item" :label="$t('release.stepDelay')">
                <InputVal :module="module" v-model:sensorValue="fastmoveData.Delay_ms" uname="time"></InputVal>
            </FormItem>
            <FormItem :zIndex="2" class="form-item" :label="$t('release.errorJump')">
                <NewSelect :module="module" :isScroll="true" :options="stepList"
                    v-model:realVal="fastmoveData.NOKAction.JumpRow"></NewSelect>
            </FormItem>
            <FormItem class="form-item" leftDis="3.8rem" :label="$t('tighten.angleReduction')">
                <LeetxSwitch :module="module" :activeVal="true" :staticVal="false" :height="2"
                    :value="fastmoveData.ActionPara.AngleZero" v-model="fastmoveData.ActionPara.AngleZero"></LeetxSwitch>
            </FormItem>
        </div>
        <LeetxButton :module="module" width="30.3125rem" height="3.75rem" @click="confirm" textColor="#fff"
            :text="$t('fastrundown.save')" fontFamily="'SourceHanSansCN-Bold'" fontSize="2.25rem" bgcColor="#00c155"
            class="confirm-box"></LeetxButton>
    </div>
</template>
<script>
import { deepCopy } from "@/utils/deepCopy";
import LeetxButton from "../controls/LeetxButton.vue";
import { fastmoveTorqueMode, fastmoveAngleMode } from './fastMove'
import NewSelect from "../select/NewSelect.vue";
export default {
    components: {
        LeetxButton,
        NewSelect,
    },
    data() {
        return {
            module: "PROFILE",
            fastmoveData: {},
            dataMethod: 0,
            hasSave: true,
            fitMethods: { : 0, : 1 },
            activeTab: 1,
            watcher: null
        };
    },
    methods: {
        confirm() {
            this.$emit('confirm')
        },
        sensorUnit(uname) {
            return this.$store.state.sensors[uname].name
        },
        cal_acc() {
            if (this.fastmoveData.ActionPara.AccTime == 0)
                this.fastmoveData.ActionPara.AccTime = 0.000001
            this.fastmoveData.ActionPara.Acc.Constant = this.fastmoveData.ActionPara.Velocity.Constant / this.fastmoveData.ActionPara.AccTime
        },
        modeChanged(mode) {
            this.dataMethod = mode
            this.fastmoveData = this.dataMethod == 1 ? fastmoveTorqueMode() : fastmoveAngleMode();
        },
        initData() {
            this.fastmoveData = deepCopy(this.stepData);
            this.dataMethod = this.stepData.Action == "ForceOpenLoop" ? 1 : 0;

        },
        updateData(key, data) {
            if (key == "JumpRow") {
                this.fastmoveData.NOKAction.JumpRow = data;
            }
        },
        sendDataStatus() {
            this.$emit("update:stepData", this.fastmoveData);
        },
        initOptions() {
            let angle = this.$t("fastrundown.angleMethod");
            let torque = this.$t("fastrundown.torqueMethod");
            this.fitMethods = { [angle]: 0, [torque]: 1 };
        },
    },
    props: {
        stepData: {
            type: Object,
            default: null,
        },
        methodType: {
            type: Number,
            default: 0,
        },
        stepList: {
            type: Object,
            default: function () {
                return { : -1, : -2 };
            },
        },
    },
    created() {
        this.initOptions();
        this.initData();
        this.watcher = this.$watch(() => this.fastmoveData, () => {
            this.sendDataStatus();
        }, { deep: true, immediate: false })
    },
    watch: {

    },

    computed: {},
};
</script>
<style scoped>
.data-box {
    position: relative;
    width: 100%;
    height: 64.2969vh;
    overflow: hidden;
}

.lock-tab {
    position: relative;
    width: 88vw;
    left: 50%;
    transform: translateX(-50%);
    height: 4.375vh;
    display: flex;
    margin-bottom: 2.1625rem;
}

.lock-tab>div {
    position: relative;
    font-size: var(--font1_75);
    flex: 1;
}

.zero {
    position: absolute;
    top: 39.6875rem;
    left: -9.85rem;
    width: 100%;
    height: 100%;
    font-size: 1.25rem;
    color: #969696;
}

.tab-text {
    display: block;
    width: auto;
    position: absolute;
    left: 50%;
    font-size: var(--font1_75);
    top: 50%;
    transform: translate(-50%, -50%);
    border-bottom: 4px solid transparent;
    white-space: nowrap !important;
}

.border-green {
    border-bottom: 4px solid var(--C00c155);
}

.form-item {
    position: relative;
    width: 100%;
    height: 3.9063vh;
    box-sizing: border-box;
    /* margin-top: 1.7188vh; */
    margin-bottom: 1.2rem;
    left: 5vw;
}

.pro-tex {
    position: relative;
    top: 1rem;
    left: 2rem;
}

.fastmove-icon {
    position: absolute;
    left: 13.85rem;
    top: 15rem;
}

.select-cnrl {
    z-index: 3;
}


.confirm-box {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 3.4688vh;
}

.target-torque {
    position: absolute;
    left: 1rem;
    top: 31rem;
}

.safe-torque {
    position: absolute;
    left: 1rem;
    top: 27rem;
}

.torque-unit {
    position: absolute;
    top: 18rem;
    left: 16rem;
    font-size: 1.25rem;
}

.angle-unit {
    position: absolute;
    left: 42.5rem;
    top: 40rem;
    font-size: 1.25rem;
}

.limit-torque {
    position: absolute;
    left: 1rem;
    top: 36rem;
}

.target-angle {
    position: absolute;
    left: 19.3rem;
    top: 41rem;
}</style>
