<template>
    <div class="form-box">
        <FormItem class="form-item  first-item" :label="$t('release.stepDelay')">
            <InputVal :module="module" uname="time" v-model:sensorValue="waitData.Delay_ms"></InputVal>
        </FormItem>
        <FormItem class="form-item" :label="$t('release.errorJump')">
            <NewSelect :module="module" :isScroll="true" :options="stepList" v-model:realVal="waitData.NOKAction.JumpRow"></NewSelect>
        </FormItem>
    </div>
</template>
<script>
import { deepCopy } from '@/utils/deepCopy'
import { waitForSignalData } from './wait'
export default {
    components: {
    },
    data() {
        return {
            module: "PROFILE",
            waitData: waitForSignalData(),
        }
    },
    methods: {
        initData() {
            this.waitData = deepCopy(this.stepData)
        },
        sendDataStatus() {
            this.$emit('update:stepData', this.waitData);
        },
    },
    props: {
        stepData: {
            type: Object,
            default: waitForSignalData()
        },
        stepList: {
            type: Object,
            default: function () {
                return { "": -1, "": -2 }
            }
        }
    },
    created() {
        this.initData()
        this.$watch(() =>this.waitData, () => {
            this.sendDataStatus();
        },{deep:true})
    },
    watch: {

    },
    computed: {

    }
}
</script>
<style scoped>
.form-item {
    position: relative;
    width: 100%;
    height: 3.9063vh;
    box-sizing: border-box;
    margin-bottom: 1.2rem;
    left: 5vw;
    z-index: 1;
}
.first-item {
    margin-top: 3.7484vh;
}

.form-box {
    margin-top: 1.25rem;
    position: relative;
}
</style>